const THREE = require("three");
// 模型渲染基本配置
const getBasicModelLoaderConfig = () => ({
  cameraPosition: {
    // 照相机位置
    x: 700,
    y: 700,
    z: 0,
  },
  cameraPositionLeft: {
    // 照相机位置: 1.初始调整根据,新控制器方位 2. 初始设置的网格方位旋转
    x: -300,
    y: 600,
    z: 300,
  },
  // 电脑兼容性配置
  computerConfig: {
    // 横屏
    HorizontalScreen: [1280],
    // 竖屏
    VerticalScreen: [1024, 960],
  },
  defaultCameraOption: {
    // 照相机默认配置
    fov: 45,
    near: 0.1,
    far: 2500,
  },
  defaultCameraOption1: {
    // 照相机默认配置
    fov: 60,
    near: 10,
    far: 5000,
  },
  // 坐标轴尺寸
  axesSize: 250,
  gridOption: {
    // 网格
    size: 1400,
    division: 60,
    color1: 0xffffff,
    color2: 0xffffff,
  },
  // 场景y轴平移距离
  sceneTranslateY: -120,
  // 环境光颜色
  ambientLightColor: 0xffffff,
  directionLightDistance: 2000,
  pointLightDistance: 5000,
  // 渲染器刷新色
  rendererClearColor: "rgba(94, 132, 201, 1)",
  // 物体尺寸边界
  meshBaseSize: 280,
  // PBR材质: MeshPhysicalMaterial
  defaultMaterial: new THREE.MeshPhysicalMaterial({
    // 物体颜色
    color: 0x0066cb,
    metalness: 0.66,
    roughness: 0.6,
    reflectivity: 1,
    side: THREE.DoubleSide,
    clearcoat: 0.5,
    clearcoatRoughness: 0.6,
  }),
  AXES: {
    X: "axes_x",
    Y: "axes_y",
    Z: "axes_z",
  },
});
const config = getBasicModelLoaderConfig();
export default config;
