const THREE = require("three");
const _box = new THREE.Box3();

function CBoxHelper(object, color) {
  this.object = object;

  if (color === undefined) color = 0xffff00;

  //const indices = new Uint16Array( [ 0, 1, 1, 2, 2, 3, 3, 0, 4, 5, 5, 6, 6, 7, 7, 4, 0, 4, 1, 5, 2, 6, 3, 7 ] );
  //const positions = new Float32Array( 8 * 3 );
  const indices = new Uint16Array([
    0, 1, 0, 2, 0, 3, 4, 5, 4, 6, 4, 7, 8, 9, 8, 10, 8, 11, 12, 13, 12, 14, 12,
    15, 16, 17, 16, 18, 16, 19, 20, 21, 20, 22, 20, 23, 24, 25, 24, 26, 24, 27,
    28, 29, 28, 30, 28, 31,
  ]);
  const positions = new Float32Array(8 * 3 * 4);

  const geometry = new THREE.BufferGeometry();
  geometry.setIndex(new THREE.BufferAttribute(indices, 1));
  geometry.setAttribute("position", new THREE.BufferAttribute(positions, 3));

  THREE.LineSegments.call(
    this,
    geometry,
    new THREE.LineBasicMaterial({ color: color, toneMapped: false })
  );

  this.type = "CBoxHelper";

  this.matrixAutoUpdate = false;

  this.update();
}

CBoxHelper.prototype = Object.create(THREE.LineSegments.prototype);
CBoxHelper.prototype.constructor = CBoxHelper;

CBoxHelper.prototype.update = function (object) {
  if (object !== undefined) {
    console.warn("THREE.CBoxHelper: .update() has no longer arguments.");
  }
  var mat4I = new THREE.Matrix4();
  if (this.object !== undefined) {
    var geometry = this.object.geometry.clone();
    var matrix = this.object.matrix.clone().setPosition(0, 0, 0);
    geometry.applyMatrix4(matrix);
    geometry.computeBoundingBox();
    _box.copy(geometry.boundingBox);

    mat4I.getInverse(matrix);

    //_box.setFromObject( this.object );
  }

  if (_box.isEmpty()) return;

  const min = _box.min;
  const max = _box.max;

  /*
	  5____4
	1/___0/|
	| 6__|_7
	2/___3/

	0: max.x, max.y, max.z
	1: min.x, max.y, max.z
	2: min.x, min.y, max.z
	3: max.x, min.y, max.z
	4: max.x, max.y, min.z
	5: min.x, max.y, min.z
	6: min.x, min.y, min.z
	7: max.x, min.y, min.z
	*/
  var xd = (max.x - min.x) / 6;
  var yd = (max.y - min.y) / 6;
  var zd = (max.z - min.z) / 6;
  const position = this.geometry.attributes.position;
  const array = position.array;

  array[0] = max.x;
  array[1] = max.y;
  array[2] = max.z; //0
  array[3] = max.x - xd;
  array[4] = max.y;
  array[5] = max.z; //0
  array[6] = max.x;
  array[7] = max.y - yd;
  array[8] = max.z; //0
  array[9] = max.x;
  array[10] = max.y;
  array[11] = max.z - zd; //0

  array[12] = min.x;
  array[13] = max.y;
  array[14] = max.z; //4
  array[15] = min.x + xd;
  array[16] = max.y;
  array[17] = max.z; //4
  array[18] = min.x;
  array[19] = max.y - yd;
  array[20] = max.z; //4
  array[21] = min.x;
  array[22] = max.y;
  array[23] = max.z - zd; //4

  array[24] = min.x;
  array[25] = min.y;
  array[26] = max.z; //8
  array[27] = min.x + xd;
  array[28] = min.y;
  array[29] = max.z; //8
  array[30] = min.x;
  array[31] = min.y + yd;
  array[32] = max.z; //8
  array[33] = min.x;
  array[34] = min.y;
  array[35] = max.z - zd; //8

  array[36] = max.x;
  array[37] = min.y;
  array[38] = max.z; //12
  array[39] = max.x - xd;
  array[40] = min.y;
  array[41] = max.z; //12
  array[42] = max.x;
  array[43] = min.y + yd;
  array[44] = max.z; //12
  array[45] = max.x;
  array[46] = min.y;
  array[47] = max.z - zd; //12

  array[48] = max.x;
  array[49] = max.y;
  array[50] = min.z; //16
  array[51] = max.x - xd;
  array[52] = max.y;
  array[53] = min.z; //16
  array[54] = max.x;
  array[55] = max.y - yd;
  array[56] = min.z; //16
  array[57] = max.x;
  array[58] = max.y;
  array[59] = min.z + zd; //16

  array[60] = min.x;
  array[61] = max.y;
  array[62] = min.z; //20
  array[63] = min.x + xd;
  array[64] = max.y;
  array[65] = min.z; //20
  array[66] = min.x;
  array[67] = max.y - yd;
  array[68] = min.z; //20
  array[69] = min.x;
  array[70] = max.y;
  array[71] = min.z + zd; //20

  array[72] = min.x;
  array[73] = min.y;
  array[74] = min.z; //24
  array[75] = min.x + xd;
  array[76] = min.y;
  array[77] = min.z; //24
  array[78] = min.x;
  array[79] = min.y + yd;
  array[80] = min.z; //24
  array[81] = min.x;
  array[82] = min.y;
  array[83] = min.z + zd; //24

  array[84] = max.x;
  array[85] = min.y;
  array[86] = min.z; //28
  array[87] = max.x - xd;
  array[88] = min.y;
  array[89] = min.z; //28
  array[90] = max.x;
  array[91] = min.y + yd;
  array[92] = min.z; //28
  array[93] = max.x;
  array[94] = min.y;
  array[95] = min.z + zd; //28

  this.geometry.applyMatrix4(mat4I);
  position.needsUpdate = true;

  this.geometry.computeBoundingSphere();
};

CBoxHelper.prototype.setFromObject = function (object) {
  this.object = object;
  this.update();

  return this;
};

CBoxHelper.prototype.copy = function (source) {
  THREE.LineSegments.prototype.copy.call(this, source);

  this.object = source.object;

  return this;
};

CBoxHelper.prototype.clone = function () {
  return new this.constructor().copy(this);
};

export { CBoxHelper };
