const THREE = require("three");
const EventEmitter = require("events");
import { GCodeLoader } from "./GCodeLoader";
import CxMesh from "./gcodeCxmesh";
// import * as TrackballControls from "./TrackballControls";
const OrbitControls = require('./OrbitControls.js')
import config from "./cxSceneConfig.js";
// 添加work通信版本的模型加载器
// import ModelsLoader from "../cx-scene/lib/ModelsLoader";
import Worker from "./wasm.worker";
// JS方式实现Gcode动画
import { GcodeRouteHelper } from "./helpers";
// 引入文件加载方法
import {loadFile} from "@/utils/public"
/* --模型渲染器-- */
export default class ModelLoader extends EventEmitter {
  constructor({
    // 容器id
    elId,
    // 是否添加控制器
    control = true,
    // 是否添加坐标轴
    axes = false,
    // 是否添加包围盒
    boundingBox = false,
    // 是否添加网格
    grid = false,
    // 画布宽度
    width,
    // 画布高度
    height,
  }) {
    super();
    // 初始化参数
    this.frameId = elId;
    this.formatSlice = [];
    this.control = control;
    this.needAxes = axes;
    this.needBoundingBox = boundingBox;
    this.needGrid = grid;
    this.boxInfo = null;
    // 固定预览区域大小
    this.machineSize = {
      x: 1400,
      y: 1400,
      z: 1000,
    };
    // this.loaderBin = new ModelsLoader();
    // gcode预览
    this.loaderGcode = new GCodeLoader(this);
    // 法线参数
    this.routerLine = new GcodeRouteHelper();
    // 初始化场景
    this.size = this.initSize(width, height);
    this.scene = new THREE.Scene();
    this.renderer = this.createRenderer();
    // 初始相机设置
    this.camera = this.createCamera();
    const { x, y, z } = config.cameraPosition;
    this.camera.position.set(x, y, z);
    // 更改控制器默认旋转方向(默认绕 Y 轴旋转)
    this.camera.up.set(0.0, 0.0, 1.0);
    // 移除three环境光照采用shader打光
    this.initLights();
    this.createRenderScene();
  }
  // 创建场景
  createRenderScene() {
    THREE.BufferGeometry.prototype.addAttribute =
      THREE.BufferGeometry.prototype.setAttribute;
    this.camera.lookAt(this.scene.position);
    this.scene.add(this.camera);
    if (this.control) {
      this.controls = new OrbitControls(
        this.camera,
        this.renderer.domElement
      );
      // 相机距离远点最远距离
      this.controls.enableDamping = false
      this.controls.dampingFactor = 0.8
      this.controls.enablePan = true
      this.controls.minDistance = 10
      this.controls.maxDistance = 1500
      // 更改控制器交互: 右键旋转,左键平移,中键缩放 THREE.MOUSE.PAN
      this.controls.mouseButtons = {
        LEFT: THREE.MOUSE.PAN,
        RIGHT: THREE.MOUSE.ROTATE,
      }
      this.controls.enableZoom = true
      this.controls.enableKeys = false
    }
    // 创建地面网格
    this.createGrind()
    this.viewFromFront()
    this.needAxes && this.scene.add(new THREE.AxesHelper(config.axesSize));
    let hasControl = true;
    this.renderer.render(this.scene, this.camera);
    let spotlight = new THREE.PointLight(0xffffff, 1);
    this.scene.add(spotlight);
    this.animate = () => {
      this.cycleId = requestAnimationFrame(this.animate);
      if (this.control || hasControl) {
        hasControl = false;
        this.controls && this.controls.update();
      }
      // 点光源跟随相机移动
      spotlight.position.copy(this.camera.position);
      this.renderer?.clear();
      this.renderer.render(this.scene, this.camera);
    };
    this.animate();
    // 监听窗口变化
    this.handleResize = () => {
      const { width, height } = this.initSize();
      this.resize(width, height);
    };
    window.addEventListener("resize", this.handleResize);
    // 引入work通信
    var worker = new Worker();
    worker.postMessage({
      cmd: "load",
    });
    var cxScene = this;
    worker.onmessage = function (event) {
      if (event.data.cmd == "loaded") {
        cxScene.emit("init");
      }
    };
    worker.addEventListener("message", function (event) {
      cxScene.emit(event.data.cmd, event.data);
    });
    this.worker = worker;
  }
  /* 创建相机 */
  createCamera({ fov, near, far } = config.defaultCameraOption1) {
    const { width, height } = this.size;
    return new THREE.PerspectiveCamera(fov, width / height, near, far);
  }
  /* 创建渲染器 */
  createRenderer() {
    const { width, height } = this.size;
    const renderer = new THREE.WebGLRenderer({
      antialias: true,
      alpha: true,
      preserveDrawingBuffer: true,
      depth: true,
    });
    renderer.setSize(width, height);
    // 给渲染器画布透明度让其承载background
    renderer.setFaceCulling(THREE.CullFaceNone, THREE.FrontFaceDirectionCW);
    // renderer.setClearColor(new THREE.Color(config.rendererClearColor));
    // 正常的物理光线
    renderer.physicalCorrectLights = true;
    // 页面容器挂载上renderer渲染器生成的画布上
    document.getElementById(this.frameId).appendChild(renderer.domElement);
    return renderer;
  }
  // 模型复位
  modelReset() {
    // 相机复位
    this.camera.position.set(725, 700, 100);
    if (this.control) {
      this.controls.target = new THREE.Vector3(
        this.machineSize.x / 2 + +this.modelSize.xSize / 4,
        this.machineSize.y / 2,
        35
      );
    }
    // this.controls.changeViewDir(this.controls.viewdirs.FRONT)
    // this.viewFromBottom()
    this.controls.object.updateProjectionMatrix();
    this.controls.update();
  }
  // 防止模型被拉伸变形
  preventStretch() {
    const canvas = this.renderer.domElement;
    this.camera.aspect = canvas.clientWidth / canvas.clientHeight;
    this.camera.updateProjectionMatrix();
    // 渲染场景中的模型
    this.renderer.render(this.scene, this.camera);
  }
  // 清除坐标轴线
  removeCube(removeObj, meshType) {
    const allChildren = this.scene.children;
    // 天空盒(name)
    if (meshType) {
      let skyMesh = allChildren.filter((child) => {
        return child.name === meshType;
      });
      this.scene.remove(skyMesh[0]);
      // 常规Mesh
    } else {
      allChildren.forEach((child) => {
        if (child.type === removeObj) {
          this.scene.remove(child);
        }
      });
    }
  }
  viewFromFront() {
    this.controls.changeViewDir(this.controls.viewdirs.FRONT)
  }
  viewFromBack() {
    this.controls.changeViewDir(this.controls.viewdirs.BACK)
  }
  viewFromLeft() {
    this.controls.changeViewDir(this.controls.viewdirs.LEFT)
  }
  viewFromRight() {
    this.controls.changeViewDir(this.controls.viewdirs.RIGHT)
  }
  viewFromTop() {
    this.controls.changeViewDir(this.controls.viewdirs.TOP)
  }
  viewFromBottom() {
    this.controls.changeViewDir(this.controls.viewdirs.BOTTOM)
  }
  // 创建地面网格
  createGrind() {
    if (!this.gridHelper) {
      const { size, division, color1, color2 } = config.gridOption;
      this.gridHelper = new THREE.GridHelper(size, division, color1, color2);
      this.gridHelper.material.transparent = true;
      this.gridHelper.material.opacity = 0.68;
      // 网格适应新的控制器方位
      this.gridHelper.rotation.x = -Math.PI / 2;
      // 网格位置调整
      this.gridHelper.rotation.y = -Math.PI;
      this.gridHelper.position.set(size / 2, size / 2, 0);
      this.scene.add(this.gridHelper);
    }
  }
  cleanAll() {
    // 释放所有资源
    window.removeEventListener("resize", this.handleResize);
    this.animate = null;
    cancelAnimationFrame(this.cycleId);
    // 释放材质资源
    if (this.mesh) {
      this.mesh.geometry.dispose();
      this.mesh.material.dispose();
      this.scene.remove(this.mesh);
    }
    // 移除光照
    if (this.lights && this.lights.length) {
      this.lights.forEach((light) => this.scene.remove(light));
    }
    // 移除相机
    this.scene.remove(this.camera);
    // 移除轨道控制器
    if (this.controls) this.controls.dispose();
    // 移除场景
    if (this.scene) this.scene.dispose();
    this.renderer.forceContextLoss();
    this.renderer.domElement = null;
    this.renderer.dispose();
    this.renderer = null;
  }
  changeModelFile(fileUrl) {
    const fileUrlArray = fileUrl.split(".");
    return fileUrlArray[fileUrlArray.length - 1];
  }
  // 单个模型渲染
  async renderFromUrl(url, onProgress, onFinish) {
    const _this = this;
    console.log('什么网格数据', this.scene.children)
    const model = {
      url,
      name: "modelPreview",
    };
    // /******** 重复加载资源判定,缓存资源 ********/
    // if (!window.stayMeshList) {
    //   window.stayMeshList = [];
    // }
    // let loadFlag = true;
    // let loadedMesh = [];
    // // 查重PUSH
    // window.stayMeshList.forEach((item) => {
    //   if (item.url === url) {
    //     loadFlag = false;
    //     loadedMesh.push(item.meshObj);
    //   }
    // });
    // 如果之前加载过的,直接渲染
    // if (!loadFlag) {
    //   console.log('加载过吗')
    //   window.modelFileRight = true;
    //   loadedMesh.forEach((iten) => {
    //     // 设置材质,材质重置
    //     iten.material.dispose();
    //     iten.material = iten.ModelShaderMaterial;
    //     // 模型加载入场景
    //     this.scene.add(iten);
    //   });
    //   // mesh都有了,直接开整
    //   _this.emit("formatSlice", window.stayMeshList[0].formatSlice);
    //   if (onFinish && onFinish instanceof Function) onFinish();
    //   _this.mesh = loadedMesh[0];
    //   // 初始渲染重置、调整参数
    //   _this.firstRenderReste();
    //   return;
    // }
    /******** 重复加载资源判定,缓存资源 ********/
    // 区分不同格式的模型文件渲染
    // let modelFile = this.changeModelFile(url)
    // gcode文件渲染
    // 矩形方块gcode文件
    // url = 'https://file-cdn-dev.creality.com/gcodePreview/fangKu.gcode'
    // url = ""
    // 高塔gcode文件
    // url = "https://file-cdn-dev.creality.com/gcodePreview/tallTower.gcode";
    //  url = require('../../assets/scoure/test001.gcode')
    this.loaderGcode.load(
      url,
      (geometry) => {
        this.renderMesh(url, model, geometry);
      },
      // 最终层数数据返回
      function (layers) {
        _this.emit("formatSlice", layers);
        // 关闭加载loading
        if (onFinish && onFinish instanceof Function) onFinish();
      },
      onProgress
    );
  }
  // 前台监听 步数数据, 通过动态改变shader参数达到改变gcode动画效果
  changeUniformsStatus(stepCount1, stepCount2) {
    console.log("正是改变gcode动画咯");
    if (this.mesh.ModelShaderMaterial) {
      this.mesh.ModelShaderMaterial.uniforms.clipValue.value =
        new THREE.Vector4(stepCount1, stepCount2, 0, 0);
    }
  }
  // 初始渲染重置、调整参数
  firstRenderReste() {
    this.setModelSize();
    this.modelReset();
  }
  // 渲染网格模型
  renderMesh(modelUrl, model, geometry) {
    let mesh = new CxMesh(model, this, geometry);
    mesh = ModelLoader.rebuildObject(mesh);
    this.mesh = mesh;
    this.renderer.clear();
    this.renderer.render(this.scene, this.camera);
    // 初始渲染重置、调整参数
    this.firstRenderReste();
    // // 缓存加载过的模型mesh
    // const meshObj = {
    //   url: modelUrl,
    //   meshObj: mesh,
    //   uuid: mesh.uuid,
    // };
    // let pushFlag = true;
    // // 查重PUSH
    // window.stayMeshList.forEach((item) => {
    //   if (item.url === modelUrl && item.uuid === mesh.uuid) {
    //     pushFlag = false;
    //   }
    // });
    // if (pushFlag) window.stayMeshList.push(meshObj);
    // gcode文件添加至场景
    this.scene.add(mesh);
  }
  // 模型变换
  static rebuildObject(mesh) {
    // 缩放
    this.boxInfo = this.boxInfo
      ? this.boxInfo
      : new THREE.Box3().setFromObject(mesh);
    const boxX = Math.abs(this.boxInfo.max.x - this.boxInfo.min.x);
    const boxY = Math.abs(this.boxInfo.max.y - this.boxInfo.min.y);
    // 原点归零: 模型中心点位于非原点位置场景, 1. 包围盒的x 方向min的position + x方向的模型长度(xmax - xmin) / 2 2. Y轴同理
    mesh.position.set(
      -this.boxInfo.min.x - boxX / 2 + 700,
      -this.boxInfo.min.y - boxY / 2 + 700,
      0
    );
    return mesh;
  }
  // 更新大小
  resize(width, height) {
    // 重新设置相机的宽高比
    this.camera.aspect = width / height;
    // 更新相机投影矩阵
    this.camera.updateProjectionMatrix();
    // 更新渲染器大小
    this.renderer.setSize(width, height);
  }
  /* 灯光 */
  initLights() {
    this.lights = [];
    const light2 = new THREE.HemisphereLight(0xffffff, 0xffffff, 1);
    light2.position.set(0, 1, 0);
    this.lights.push(light2);
    this.scene.add(light2);
    this.lights.forEach((light) => this.scene.add(light));
  }
  // 方向光
  initDirectionalLight() {
    const light = new THREE.DirectionalLight(0xff0000);
    light.position.copy(this.camera.position);
    this.lights.push(light);
  }
  // 点光源
  initPointLight(x, y, z) {
    const light = new THREE.PointLight(0xffffff, 1);
    light.position.set(x, y, z);
    this.lights.push(light);
  }
  // 设置画布尺寸
  initSize(w, h) {
    let width, height;
    if (w && h) {
      width = w;
      height = h;
    } else {
      const frame = document.getElementById(this.frameId);
      width = frame.clientWidth;
      height = frame.clientHeight;
    }
    this.size = {
      width,
      height,
    };
    return this.size;
  }
  setModelSize() {
    // 计算模型尺寸
    const box = new THREE.Box3().setFromObject(this.mesh);
    const xSize = (box.max.x - box.min.x).toFixed(2);
    const ySize = (box.max.y - box.min.y).toFixed(2);
    const zSize = (box.max.z - box.min.z).toFixed(2);
    this.modelSize = {
      xSize,
      ySize,
      zSize,
    };
  }
}
