/**
 * GCodeLoader is used to load gcode files usually used for 3D printing or CNC applications.
 *
 * Gcode files are composed by commands used by machines to create objects.
 *
 * @class GCodeLoader
 * @param {Manager} manager Loading manager.
 * @author tentone
 * @author joewalnes
 */
const THREE = require("three");
import {
  BufferGeometry,
  FileLoader,
  Float32BufferAttribute,
  Loader,
} from "three";
// 引入pako解压与压缩字符串
import pako from "pako";
var reqUrl = require("url");
var GCodeLoader = function (cxscene, manager) {
  Loader.call(this, manager);

  this.splitLayer = false;
  this.formatSlice = [];
  this.cxscene = cxscene;
};

GCodeLoader.prototype = Object.assign(Object.create(Loader.prototype), {
  constructor: GCodeLoader,

  load: function (url, onLoad, layersCallback, onProgress, onError) {
    var scope = this;
    var loader = new FileLoader(scope.manager);
    // 资源尾缀,解析一统
    // var path = reqUrl.parse(url, true).path;
    // var ext = path.substring(path.lastIndexOf("."), path.length).toLowerCase();
    // if (ext === '.gz' || ext === '.cxline') {
    // 	loader.setResponseType("arraybuffer");
    // }
    loader.setPath(scope.path);
    loader.load(
      url,
      function (text) {
        let finallyText = text;
        // 处于gz压缩包状态,需要先解压。
        // if (ext === '.gz') {
        // 	// 解压gcode.gz压缩包文件
        // 	let gcodeInfo = pako.ungzip(text, { to: 'string' });
        // 	finallyText = gcodeInfo
        // }
        try {
          scope.parse(finallyText, onLoad, layersCallback);
        } catch (e) {
          if (onError) {
            onError(e);
          } else {
            console.error(e);
          }
          scope.manager.itemError(url);
        }
      },
      onProgress,
      onError
    );
  },
  create: function (model) {},
  parse: function (data, onload, layersCallback) {
    const _this = this;
    let CMD_RESULT = {
      GCODE_CMD: "gcodeParse",
      GCODE_RESULT: "gcodeParseResult",
      GCODE_HEAD_RESULT: "gcodeHeadResult",
    };
    // 定义初始预解析函数
    var funParse = function (data, meshlistener, headerListener) {
      // headerListener分段结束,最终层数数据截取
      _this.cxscene.once(CMD_RESULT.GCODE_HEAD_RESULT, headerListener);
      // meshlistener分段结束数据截取
      _this.cxscene.off(CMD_RESULT.GCODE_RESULT, meshlistener);
      // meshlistener分段开始数据截取
      _this.cxscene.on(CMD_RESULT.GCODE_RESULT, meshlistener);
      _this.cxscene.worker.postMessage({
        cmd: CMD_RESULT.GCODE_CMD,
        buffer: data,
      });
    };
    // 监听算法解析完成标识,获取返回的数据: 分段数据获取
    var meshlistener = function (data) {
      var geometry = addGcodeObject(
        new Float32Array(data.data.positionEndArray),
        new Float32Array(data.data.positionBeginArray),
        new Float32Array(data.data.stepArray),
        new Float32Array(data.data.flagArray)
      );
      onload(geometry);
    };
    // 分段完成,最终层数返回
    var headerlistener = function (data) {
      // 层数数据截取
      _this.formatSlice = new Int32Array(data.data.layers);
      layersCallback(_this.formatSlice);
    };
    // 发送算法解析work指令,坐等数据回传
    funParse(data, meshlistener, headerlistener);
    function addGcodeObject(
      vertexEndPosition,
      vertexBeginPosition,
      vertexFlag,
      vertexDrawFlag
    ) {
      var positions = new Float32Array((vertexEndPosition.length / 4) * 3);
      var posIndexLength = positions.length / 3;
      for (var i = 0; i < posIndexLength; i++) {
        positions[i * 3] = vertexBeginPosition[i * 2];
        positions[i * 3 + 1] = vertexBeginPosition[i * 2 + 1];
        positions[i * 3 + 2] = vertexEndPosition[i * 4 + 2];
      }
      var geometry = new BufferGeometry();
      geometry.setAttribute(
        "position",
        new Float32BufferAttribute(positions, 3)
      );
      // 结束位置
      geometry.setAttribute(
        "vertexEndPosition",
        new Float32BufferAttribute(vertexEndPosition, 4)
      );
      // 开始位置
      geometry.setAttribute(
        "vertexBeginPosition",
        new Float32BufferAttribute(vertexBeginPosition, 2)
      );
      // 步数
      geometry.setAttribute(
        "vertexStep",
        new Float32BufferAttribute(vertexFlag, 1)
      );
      // 改变gcode预览外观类型
      geometry.setAttribute(
        "vertexFlag",
        new Float32BufferAttribute(vertexDrawFlag, 1)
      );
      return geometry;
    }
  },
});
export { GCodeLoader };
