const THREE = require("three");
export default class ConvexHelper {
  constructor(mesh) {
    // this.points = points;
    this.mesh = mesh;
  }

  createConvexHelper(arrayOfPoints) {
    let material, splineObject, geometry;
    geometry = new THREE.BufferGeometry();
    geometry.setAttribute(
      "position",
      new THREE.BufferAttribute(arrayOfPoints, 3)
    );
    //var geometry = new THREE.BufferGeometry().setFromPoints(points);
    material = new THREE.LineBasicMaterial({ color: 0xff0000 });
    splineObject = new THREE.LineSegments(geometry, material);
    return splineObject;
  }

  createFaceHelper(arrayOfPoints) {
    let material, faceMesh, geometry;
    geometry = new THREE.BufferGeometry();
    geometry.setAttribute(
      "position",
      new THREE.BufferAttribute(arrayOfPoints, 3)
    );
    material = new THREE.MeshBasicMaterial({ color: 0xff0000 });
    faceMesh = new THREE.Mesh(geometry, material);
    return faceMesh;
  }
}
