const THREE = require("three");
import GcodeShader from "./GcodeShader";
import { CBoxHelper } from "./helpers/CBoxHelper.js";
var CxMesh = function (model, cxScene, geometry) {
  // 模型顶点平滑化
  geometry.computeVertexNormals();
  // 计算模型包围盒
  geometry.computeBoundingBox();
  // 定义模型初始加载名称
  this.modelname = model.name ? model.name : model.modelname;
  this.cloneNum = model.cloneNum || 1;
  this.cxScene = cxScene;
  this.bSelected = false;
  geometry.name = "modelList";
  this.ModelShaderMaterial = new THREE.ShaderMaterial({
    uniforms: GcodeShader.uniforms,
    vertexShader: GcodeShader.vertexShader,
    fragmentShader: GcodeShader.fragmentShader,
    side: THREE.DoubleSide,
    // 是否关闭深度检测
    depthTest: true,
  });
  THREE.Mesh.call(this, geometry, this.ModelShaderMaterial);
  this.updateMatrix();
  // 周围边框
  this.initBboxHelper = function () {
    // 选中模型包围框的颜色
    this.bboxHelper = new CBoxHelper(this, 0xfff0f5);
    this.bboxHelper.visible = true;
    this.bboxHelper.geometry.computeBoundingBox();
    // 包围盒自定义名称
    this.bboxHelper.geometry.name = "boxHelper";
    this.add(this.bboxHelper);
  };
  // this.initBboxHelper();
};
CxMesh.prototype = Object.create(THREE.Mesh.prototype);
CxMesh.prototype.constructor = THREE.Mesh;
export default CxMesh;
