<template>
  <div class="resonant_Sensor">
    <HeaderBar :title="'谐振传感器状态'" :icon="'&#xe625;'" />
    <div class="content">
      <div class="status">
        <span>谐振传感器状态</span>
        <div class="label">正常</div>
      </div>
      <div class="start">
        <el-button @click="start">开始</el-button>
        <div class="progress">
          <el-progress :percentage="50" />
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import HeaderBar from "@/components/HeaderBar";
import { onMounted, reactive, getCurrentInstance } from "vue";
const state = reactive({
  socket: null,
});

const start = () => {
  state.socket.sendMsg({
    method: "set",
    params: {
      resonanceTest: "",
    },
  });
};

onMounted(() => {
  const { proxy } = getCurrentInstance();
  state.socket = proxy.$webSocket;
});
</script>

<style lang="scss" scoped>
.resonant_Sensor {
  width: 100%;
  height: 235px;
  margin-top: 17px;
  @include theme_layout();
  .content {
    padding: 20px;
    .status {
      display: flex;
      align-items: center;
      font-size: 12px;
      .label {
        width: 40px;
        height: 30px;
        background: #347b55;
        border-radius: 5px;
        line-height: 30px;
        text-align: center;
        margin-left: 17px;
      }
    }
    .start {
      display: flex;
      align-items: center;
      margin-top: 62px;
      .progress {
        width: 555px;
        margin-left: 10px;
      }
    }
  }
}
</style>
