<template>
  <div class="Tem_adjust">
    <!-- 头部容器 -->
    <HeaderBar :title="'温度调整'" class="header" :icon="'&#xe644;'">
      <template v-slot:right>
        <el-button @click="startTemp" class="start-btn"> 开始 </el-button>
      </template>
    </HeaderBar>
    <!-- 主体容器 -->
    <div class="middleContent">
      <div class="leftPrintshow">
        <div
          :class="['btn_container']"
          v-for="(item, index) in showButtonConfig"
          :key="index"
        >
          <el-button
            @click="state.checked = item.value"
            :class="{ active: state.checked === item.value }"
          >
            {{ item.label }}
          </el-button>
        </div>
      </div>
      <!-- 中间右侧容器 -->
      <div class="rightPrintshow">
        <!-- 画布容器 -->
        <div class="temp-chart" ref="chartRef"></div>

        <!-- 尾部调控触发 -->
        <div class="foot_right">
          <div class="label_info">目标温度</div>
          <div class="inner_input">
            <el-input v-model="state.temp" />
          </div>
          <div class="inner_button">
            <el-button size="small" @click="saveTemChange">保存</el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script setup>
import { reactive, onMounted, getCurrentInstance, watch } from "vue";
import { ElMessage } from "element-plus";
import HeaderBar from "@/components/HeaderBar";
import { getNowFormatDate } from "@/utils/public";

let state = reactive({
  temp: "",
  checked: 0,
  source: [],
  product: ["product"],
  tableData: [
    {
      project: "Extruder",
    },
    {
      project: "Header Bed0",
    },
  ],
});
watch(
  () => state.checked,
  () => {
    state.temp = "";
  }
);

// 中间input 配置
const showButtonConfig = reactive([
  {
    label: "喷头PID调整",
    value: 0,
  },
  {
    label: "热床PID调整",
    value: 1,
  },
]);

const startTemp = () => {
  if (!state.temp) ElMessage.warning("目标温度不能为空");
  const value = state.checked === 0 ? 1 : 2;
  state.socket.sendMsg({
    method: "set",
    params: {
      startAutoPid: +value,
    },
  });
};
const saveTemChange = () => {
  if (!state.temp) ElMessage.warning("目标温度不能为空");
  const param = state.checked === 0 ? "nozzleTempAutoPid" : "bedTempAutoPid";
  state.socket.sendMsg({
    method: "set",
    params: {
      [param]: +state.temp,
    },
  });
};
const setOpt = () => {
  const series = state.tableData.map((m) => {
    state.product.push(m.project);
    return {
      type: "line",
      areaStyle: {
        opacity: 0.1,
      },
      smooth: true,
      symbol: "none",
    };
  });
  state.source = [state.product];
  for (let i = 60; i--; i > 0) {
    state.source.push([getNowFormatDate(i), 0, 0]);
  }
  let option = {
    color: ["#ED970F", "#289ADA"],
    title: {
      text: "temperature℃",
      left: 40,
      top: 0,
      textStyle: {
        color: "#CBCBCC",
        fontSize: 14,
      },
    },
    tooltip: {
      trigger: "axis",
      textStyle: {
        align: "left",
        color: "#dbdbdc",
      },
      backgroundColor: "#515157",
      borderColor: "#515157",
    },
    grid: {
      left: "3%",
      right: "4%",
      bottom: 10,
      top: 30,
      containLabel: true,
    },
    xAxis: {
      type: "category",
      boundaryGap: false,
      axisTick: {
        show: false,
      },
      splitLine: {
        show: true,
        lineStyle: {
          color: ["#46464A"],
        },
      },
    },
    yAxis: {
      type: "value",
      splitLine: {
        show: true,
        lineStyle: {
          color: ["#46464A"],
        },
      },
    },
    dataset: {
      source: state.source,
    },
    series,
  };
  return option;
};
onMounted(() => {
  const { proxy } = getCurrentInstance();
  state.socket = proxy.$webSocket;
  state.messageCenter = proxy.$messageCenter;
  const chartDom = document.querySelector(".temp-chart");
  let opt = setOpt();
  let chart = proxy.$echarts.init(chartDom);
  opt && chart.setOption(opt);
  state.messageCenter.on("message_change", (data) => {
    if (data === "ok") return;
    const responData = JSON.parse(data);
    const { nozzleTemp, bedTemp0 } = responData;
    if (nozzleTemp || bedTemp0) {
      state.tableData[0].temperature =
        responData.nozzleTemp || state.tableData[0].temperature; // 喷嘴当前温度
      state.tableData[1].temperature =
        responData.bedTemp0 || state.tableData[1].temperature; // 热创当前温度
      const pre = state.source[state.source.length - 1];
      let curTime = getNowFormatDate(0);
      const curData = [curTime, +nozzleTemp || pre[1], +bedTemp0 || pre[2]];
      if (pre[0] === curTime) {
        if (state.source.length === 61) {
          state.source.splice(state.source.length - 1, 1);
        } else return;
      }
      state.source.push(curData);
      if (state.source.length > 120) state.source.splice(1, 1);

      chart.setOption({
        dataset: {
          source: state.source,
        },
      });
    }
  });
  window.addEventListener("resize", () => chart.resize());
});
</script>
<style lang="scss" scoped>
.Tem_adjust {
  width: 58.8%;
  height: 417px;
  min-width: 500px;
  @include theme_layout();
  .head-show {
    width: 81%;
    .left-feature {
      width: 54px;
      padding: 12px 15px;
      font-size: 16px;
      font-weight: bold;
      color: #ffffff;
      span {
        padding-left: 5px;
      }
    }
    .right-feature {
      padding: 8px 15px;
      display: flex;
      justify-content: space-evenly;
      .head_inner {
        padding: 1% 0px 0px 1%;
        font-size: 14px;
      }
    }
  }
  .start-btn {
    height: 24px;
    width: 80px;
    border-radius: 12px;
    font-size: 12px;
  }
  .middleContent {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    .leftPrintshow {
      width: 19%;
      padding: 11px 0px 0px 20px;
      .btn_container {
        .el-button {
          width: 75%;
          height: 40px;
          box-shadow: 0px 3px 3px 0px rgba(31, 31, 35, 0.5);
          border-radius: 5px;
        }
        .el-button:nth-child(1) {
          margin-top: 10px;
        }
      }
      .active {
        background: #00a3ff;
      }
    }
    .rightPrintshow {
      width: 76%;
      background: #2d2d31;
      border-radius: 5px;
      margin: 85px 20px 20px 20px;
      position: relative;
      height: 248px;
      .temp-chart {
        width: 100%;
        height: 100%;
      }
      .foot_right {
        height: 40px;
        // border: 1px solid;
        position: absolute;
        right: -0.5%;
        bottom: 110%;
        display: flex;
        justify-content: center;
        align-items: baseline;
        .label_info {
          font-size: 14px;
          font-family: Source Han Sans CN;
          font-weight: 500;
          color: #ffffff;
        }
        .inner_input {
          width: 140px;
          height: 36px;
          border-radius: 5px;
          margin-left: 13px;
        }
        .inner_button {
          padding-left: 10px;
          .el-button {
            width: 60px;
            height: 36px;
          }
        }
      }
    }
  }
}
</style>
