watch(
  () => store.printerCfgInfo,
  () => {
    for (const s of state.showConfigList) {
      if (!s.type) continue;
      for (const i of s.electric) {
        let k = s.type,
          v = i.type;
        const keyMap = ["run_current", "hold_current", "heater_temp"];
        if (
          v &&
          !store.printerCfgInfo[k][v] &&
          keyMap.find((f) => v.includes(f))
        ) {
          let arr = v.split("-");
          k = arr[0];
          v = arr[1];
        }
        i.electricInput = store.printerCfgInfo[k][v];
        if (i.endXinput) {
          const value = store.printerCfgInfo[k][v].split(",");
          i.electricInput = value[0];
          i.endXinput = value[1];
        }
      }
    }
  }
);
// 保存配置界面的参数
const save = () => {
  let setPrinterCfg = {};
  // 配置保存交互提示
  ElMessageBox.confirm("确认保存当前配置?", "提示", {
    confirmButtonText: "确认",
    cancelButtonText: "取消",
    type: "warning",
  })
    .then(() => {
      for (const i of state.showConfigList) {
        setPrinterCfg[i.type] = {};
        for (const o of i.electric) {
          let k = i.type,
            v = o.type;
          const keyMap = ["run_current", "hold_current", "heater_temp"];
          if (v && keyMap.find((f) => v.includes(f))) {
            let arr = v.split("-");
            k = arr[0];
            v = arr[1];
            if (!setPrinterCfg[k]) setPrinterCfg[k] = {};
          }
          if (k && v) setPrinterCfg[k][v] = o.electricInput.toString();
          if (Object.keys(o).includes("endXinput")) {
            setPrinterCfg[k][v] =
              o.electricInput + "," + +o.endXinput.toString();
          }
        }
      }
      state.socket.sendMsg({
        method: "set",
        params: { setPrinterCfg },
      });
      ElMessage({
        type: "success",
        message: "保存成功",
      });
    })
    .catch(() => {
      ElMessage({
        type: "info",
        message: "已取消",
      });
    });
};
// 返回至上一次配置更改
function changeUpdate(events, item1) {
  item1.upadted = true;
}
function onInputFocus(param) {
  param.electricDefault = param.electricInput;
}
function onInputFocus1(param) {
  param.electricDefault1 = param.endXinput;
}
// 返回上次配置数据
function resetData(item1) {
  item1.electricInput = item1.electricDefault || item1.electricInput;
  item1.endXinput = item1.electricDefault1 || item1.endXinput;
  item1.upadted = false;
}

onMounted(() => {
  const { proxy } = getCurrentInstance();
  state.socket = proxy.$webSocket;
  state.messageCenter = proxy.$messageCenter;
  state.socket.sendMsg({
    method: "get",
    params: { reqPrinterCfg: 1 },
  });
