const THREE = require("three");
export default class PlaneHelper {
  constructor() {
    // this.createPlaneHelper(mesh, dir, distance);
  }

  createPlaneHelper(sel, dir, distance) {
    let size = 300;
    if (!["xy", "xz", "yz"].includes(dir)) return;
    let xyzMap = {
      xy: [size, size, 1],
      xz: [size, 1, size],
      yz: [1, size, size],
    };
    const geometry = new THREE.BoxGeometry(...xyzMap[dir]);
    geometry.name = "planeHelper";
    const material = new THREE.MeshBasicMaterial({
      color: 0xc0f0f7,
      side: THREE.DoubleSide,
    });
    const planeHelper = new THREE.Mesh(geometry, material);
    if (sel.length === 1) {
      const mesh = sel[0];
      const { max, min } = mesh.geometry.boundingBox;
      planeHelper.position.copy(mesh.position);
      const { x, y, z } = mesh.position;
      if (dir === "xz") {
        planeHelper.position.y = (distance > 0 ? max.y : min.y) + y + distance;
      } else if (dir === "xy") {
        planeHelper.position.z = (distance > 0 ? max.z : min.z) + z + distance;
      } else {
        planeHelper.position.x =
          (distance > 0 ? max + x : x + min) + x + distance;
      }
    } else {
      let xSel = [],
        ySel = [],
        zSel = [],
        leftX = sel[0],
        rigX = sel[0],
        leftY = sel[0],
        rigY = sel[0],
        leftZ = sel[0],
        rigZ = sel[0];
      for (const item of sel) {
        const { x, y, z } = item.position;
        leftX = x < leftX.position.x ? leftX : item;
        rigX = x < rigX.position.x ? item : rigX;

        leftY = y < leftY.position.y ? leftY : item;
        rigY = y < rigY.position.y ? item : rigY;

        leftZ = z < leftZ.position.z ? leftZ : item;
        rigZ = z < rigZ.position.z ? item : rigZ;

        xSel.push(x);
        ySel.push(y);
        zSel.push(z);
      }

      if (dir === "xz") {
        planeHelper.position.x = (leftX.position.x + rigX.position.x) / 2;
        planeHelper.position.z = (leftZ.position.z + rigZ.position.z) / 2;
        planeHelper.position.y =
          (distance > 0
            ? leftY.geometry.boundingBox.max.y + leftY.position.y
            : rigY.geometry.boundingBox.min.y + rigY.position.y) + distance;
      } else if (dir === "xy") {
        planeHelper.position.x = (leftX.position.x + rigX.position.x) / 2;
        planeHelper.position.y = (leftY.position.y + rigY.position.y) / 2;
        planeHelper.position.z =
          (distance > 0
            ? leftZ.geometry.boundingBox.max.z + leftZ.position.z
            : rigZ.geometry.boundingBox.min.z + rigZ.position.z) + distance;
      } else {
        planeHelper.position.z = (leftZ.position.z + rigZ.position.z) / 2;
        planeHelper.position.y = (leftY.position.y + rigY.position.y) / 2;
        planeHelper.position.x =
          (distance > 0
            ? leftX.geometry.boundingBox.max.x + leftX.position.x
            : rigX.geometry.boundingBox.min.z + rigX.position.x) + distance;
      }
    }
    planeHelper.updateMatrix();
    return planeHelper;
  }
}
