<template>
  <div class="config-manner">
    <div class="container-main">
      <!-- 头部banner -->
      <HeaderBar :title="'设备信息及管理'" :icon="'&#xe656;'" />
      <!-- 中部左侧容器: 机器信息 -->
      <div class="middle-left">
        <div
          class="middle-left-inner"
          v-for="(item, index) in state.showLeftList"
          :key="index"
        >
          <div class="device-left">
            <div>{{ item.deviceName }}</div>
            <div class="right-inn">{{ item.deviceModel }}</div>
          </div>
          <div class="device-right" v-if="item.rightInfo">
            <span
              :style="{
                color: item.rightInfoColor,
              }"
              >{{ item.rightInfo }}</span
            >
            <el-button
              v-if="item.rightBtn"
              class="spe-btn"
              @click="linuxUpgrade"
              >{{ item.rightBtn }}</el-button
            >
          </div>
        </div>
      </div>
      <!-- 底部左侧容器: 磁盘使用 -->
      <div class="footer-container">
        <div class="diskInfo">磁盘使用率:</div>
        <div class="diskFont">
          <el-progress :stroke-width="20" :percentage="70" />
        </div>
        <div class="diskRight">
          <span>41.8MB可用</span>
          <span> / 共100MB</span>
        </div>
      </div>
      <!-- 右侧容器 -->
      <div class="right-container">
        <div class="first-warp">
          <el-button @click="restartKlipper">重启上位机</el-button>
          <el-button @click="restartFirmware">重启下位机</el-button>
          <el-button @click="resetSystem">恢复出厂</el-button>
        </div>
        <!-- 选择组件 -->
        <div
          class="select-container"
          v-for="(item, index) in state.showRightSelect"
          :key="index"
        >
          <span>{{ item.showInfo }}</span>
          <el-select
            v-if="item.showSelect"
            v-model="item.selectValue"
            class="m-2"
            placeholder="Select"
            size="large"
          >
            <el-option
              v-for="ite in item.timeOptions"
              :key="ite.value"
              :label="ite.label"
              :value="ite.value"
            />
          </el-select>
          <!-- 
          <el-input
            v-if="item.showTextArea"
            v-model="item.textAreaValue"
            :rows="2"
            type="textarea"
            :placeholder="item.placeholder"
          /> -->
        </div>
        <!-- <div class="footerBtn">
          <el-button>提交反馈</el-button>
        </div> -->
      </div>
    </div>
  </div>
</template>
<script setup>
import { getCurrentInstance, onMounted, reactive, watch } from "vue";
import HeaderBar from "@/components/HeaderBar";
import { useMainStore } from "@/stores";
const store = useMainStore();
let state = reactive({
  upgradeUrl: "",
  showLeftList: [
    { deviceName: "机器型号:", deviceModel: "", type: 0 },
    { deviceName: "硬件版本:", deviceModel: "", type: 1 },
    {
      deviceName: "软件版本:",
      deviceModel: "",
      rightInfo: "您可升级版本到",
      rightBtn: "点击升级",
      rightInfoColor: "#FFFFFF",
      type: 2,
    },
  ],
  // 右侧选择数组数据
  showRightSelect: [
    {
      showInfo: "时区选择: ",
      // 选择挂载
      selectValue: 1,
      showSelect: true,
      timeOptions: [
        {
          value: 1,
          label: "东八区",
        },
        {
          value: 2,
          label: "东九区",
        },
      ],
    },
    {
      showInfo: "语言: ",
      // 选择挂载
      selectValue: 1,
      showSelect: true,
      timeOptions: [
        {
          value: 1,
          label: "中文",
        },
        {
          value: 2,
          label: "英文",
        },
      ],
    },
    // {
    //   showInfo: "意见反馈: ",
    //   showTextArea: true,
    //   // 选择挂载
    //   textAreaValue: "",
    //   placeholder: "输入您的想法~",
    // },
  ],
});

watch(
  [() => store.linuxOtaInfo, () => store.modelVersion, () => store.model],
  () => {
    if (store.linuxOtaInfo) {
      const { url, version } = store.linuxOtaInfo;
      state.upgradeUrl = url;
      for (const item of state.showLeftList) {
        if (item.type === 2) item.rightInfo = `您可升级版本到${version}`;
      }
    }
    if (store.modelVersion) {
      let arr = store.modelVersion.split(";");
      for (const item of arr) {
        if (item.includes("DWIN hw ver")) {
          let hardVer = state.showLeftList.find((f) => f.type === 1);
          hardVer.deviceModel = item.replace("DWIN hw ver:", "");
        }

        if (item.includes("DWIN sw ver")) {
          let hardVer = state.showLeftList.find((f) => f.type === 2);
          hardVer.deviceModel = item.replace("DWIN sw ver:", "");
        }
      }
    }
    if (store.model) {
      let hardVer = state.showLeftList.find((f) => f.type === 0);
      hardVer.deviceModel = store.model;
    }
  },
  {
    immediate: true,
  }
);

const linuxUpgrade = () => {
  state.socket.sendMsg({
    method: "set",
    params: { linuxUpgrade: state.upgradeUrl },
  });
};
const restartKlipper = () => {
  state.socket.sendMsg({
    method: "set",
    params: { restartKlipper: 1 },
  });
};
const restartFirmware = () => {
  state.socket.sendMsg({
    method: "set",
    params: { restartFirmware: 1 },
  });
};
const resetSystem = () => {
  state.socket.sendMsg({
    method: "set",
    params: { resetSystem: 15 },
  });
};
onMounted(() => {
  const { proxy } = getCurrentInstance();
  state.socket = proxy.$webSocket;
  state.messageCenter = proxy.$messageCenter;
  // setDeviceStatus();
  state.socket.sendMsg({
    method: "get",
    params: { reqOtaInfo: 1 },
  });
});
</script>
<style lang="scss" scoped>
.config-manner {
  padding-top: 20px;
  .container-main {
    position: relative;
    min-width: 1200px;
    width: 97.8%;
    height: 391px;
    margin: auto;
    margin-top: 56px;
    @include theme_layout();
    .middle-left {
      width: 48.6%;
      height: 246px;
      font-weight: normal;
      .middle-left-inner {
        width: 100%;
        height: 40px;
        display: flex;
        border-bottom: 1px solid #46464a;
        justify-content: flex-start;
        align-items: center;
        font-size: 14px;
        font-family: Source Han Sans CN;
        font-weight: normal;
        .device-left {
          width: 185px;
          display: flex;
          justify-content: space-between;
          padding-left: 21px;
          color: #cbcbcc;
          div:nth-child(1) {
            font-weight: normal;
          }
          .right-inn {
            width: 76px;
            text-align: left;
            color: #ffffff;
            font-weight: normal;
          }
        }
        .device-right {
          color: #ffffff;
          padding-left: 60px;
          span {
            font-weight: normal;
          }
          .spe-btn {
            width: 100px;
            height: 28px;
            margin-left: 10px;
            span {
              font-weight: normal;
            }
          }
        }
      }
    }
    .footer-container {
      width: 47.9%;
      text-align: left;
      padding-left: 21px;
      font-family: Source Han Sans CN;
      font-weight: normal;
      .diskInfo {
        font-size: 14px;
        color: #cbcbcc;
        font-weight: normal;
      }
      .diskFont {
        padding: 14px 0px 5px 0px;
        :deep().el-progress {
          .el-progress-bar {
            .el-progress-bar__outer {
              height: 10px !important;
            }
          }
        }
      }
      .diskRight {
        span:nth-child(1) {
          color: #ffffff;
          font-weight: normal;
        }
        span:nth-child(2) {
          color: #cbcbcc;
          font-weight: normal;
        }
      }
    }
    .right-container {
      width: 48%;
      height: 310px;
      position: absolute;
      top: 41px;
      right: 23px;
      padding-top: 20px;
      .first-warp {
        text-align: left;
        .el-button {
          width: 160px;
          height: 36px;
          span {
            font-size: 14px;
            font-family: Source Han Sans CN;
            font-weight: normal;
            color: #ffffff;
          }
        }
      }
    }
    .select-container {
      display: flex;
      justify-content: space-between;
      align-items: baseline;
      margin-top: 20px;
      span {
        font-size: 14px;
        font-family: Source Han Sans CN;
        font-weight: normal;
        color: #ffffff;
      }
      .el-select {
        width: 89.5%;
        height: 36px;
      }
      .el-textarea {
        width: 89.5%;
        height: 72px;
        --el-input-border-color: #46464a;
        :deep().el-textarea__inner {
          background: #37373b;
          font-size: 14px;
          font-family: Source Han Sans CN;
          font-weight: normal;
          color: #cbcbcc;
          height: 72px;
        }
      }
    }
    .footerBtn {
      width: 100px;
      height: 28px;
      padding: 10px 0px 0px 105px;
      text-align: left;
    }
  }
}
</style>
