<template>
  <div class="video_phy">
    <HeaderBar :title="'视频/摄影参数设置'" :icon="'&#xe625;'" />
    <div class="content">
      <div class="video">
        <span class="title">视频参数</span>
        <i class="line"></i>
        <div class="fps">
          <span>帧数:</span>
          <el-input> </el-input>
        </div>
        <div>
          <span>分辨率:</span>
          <el-input> </el-input>
        </div>
        <!-- <el-button class="save"> 保存 </el-button> -->
      </div>
      <div class="phy">
        <div class="title">
          <span>延时摄影</span>
          <el-switch v-model="state.elapseValue" @change="handleVideoElapse" />
        </div>
        <div class="line"></div>
        <div class="pos">
          <div>拍照位置：</div>
          <div class="btn">
            <el-button
              @click="phoChange(0)"
              :class="{ active: state.phoCheck === 0 }"
            >
              每层的起始位置
            </el-button>
            <el-button
              @click="phoChange(1)"
              :class="{ active: state.phoCheck === 1 }"
            >
              喷头移开时
            </el-button>
          </div>
        </div>
        <div class="pos">
          <div>视频帧数：</div>
          <div class="btn">
            <el-button
              @click="fpsChange(0, 15)"
              :class="{ active: state.fpsCheck === 0 }"
            >
              15帧
            </el-button>
            <el-button
              @click="fpsChange(1, 25)"
              :class="{ active: state.fpsCheck === 1 }"
            >
              25帧
            </el-button>
          </div>
        </div>
        <div class="line line1"></div>
        <div class="resolution">
          <span>每几层拍摄一次:</span>
          <el-input v-model="state.resolutionInterval" @change="saveInterval" />
        </div>
        <!-- <el-button class="save save1" @click="saveInterval"> 保存 </el-button> -->
      </div>
    </div>
  </div>
</template>

<script setup>
import HeaderBar from "@/components/HeaderBar";
import { ElMessage } from "element-plus";
import { reactive, onMounted, getCurrentInstance, watch } from "vue";
import { usePrepareStore } from "@/stores";
const store = usePrepareStore();
watch(
  [
    store.videoElapse,
    store.nozzleMoveSnapshot,
    store.videoElapseFrame,
    store.videoElapseInterval,
  ],
  () => {
    state.elapseValue = !!store.videoElapse;
    state.phoCheck = store.nozzleMoveSnapshot;
    state.fpsCheck = store.videoElapseFrame === 25 ? 1 : 0;
    state.resolutionInterval = store.videoElapseInterval;
  }
);

const state = reactive({
  socket: null,
  elapseValue: !!store.videoElapse,
  phoCheck: store.nozzleMoveSnapshot,
  fpsCheck: store.videoElapseFrame === 25 ? 1 : 0,
  resolutionInterval: store.videoElapseInterval,
});
const handleVideoElapse = (val) => {
  state.socket.sendMsg({
    method: "set",
    params: {
      videoElapse: +val,
    },
  });
};
const phoChange = (val) => {
  if (!state.elapseValue) return;
  state.phoCheck = val;
  state.socket.sendMsg({
    method: "set",
    params: {
      nozzleMoveSnapshot: +val,
    },
  });
};

const fpsChange = (val, count) => {
  if (!state.elapseValue) return;
  state.fpsCheck = val;
  state.socket.sendMsg({
    method: "set",
    params: {
      videoElapseFrame: +count,
    },
  });
};

const saveInterval = () => {
  if (!state.elapseValue) return;
  if (!state.resolutionInterval) ElMessage.warning("拍照层数不能为空");
  state.socket.sendMsg({
    method: "set",
    params: {
      videoElapseInterval: +state.resolutionInterval,
    },
  });
};

onMounted(() => {
  const { proxy } = getCurrentInstance();
  state.socket = proxy.$webSocket;
});
</script>

<style lang="scss" scoped>
.video_phy {
  width: 37.8%;
  height: 417px;
  margin-top: 17px;
  @include theme_layout();
  .content {
    width: calc(100% - 40);
    padding: 20px;
    display: flex;
    justify-content: space-between;
    .active {
      background: #00a3ff;
    }
    .video {
      width: 25%;
      min-width: 150px;
      height: 267px;
      background: #37373b;
      border: 1px solid #46464a;
      border-radius: 5px;
      padding: 20px;
      font-size: 14px;
      position: relative;
      .title {
        height: 16px;
        font-size: 16px;
        display: flex;
        justify-content: flex-start;
      }
      .line {
        display: inline-block;
        width: 100%;
        height: 1px;
        background: #46464a;
        margin: 20px 0;
      }
      > div {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;
        > div {
          width: 90px;
          height: 36px;
        }
      }
    }
    .save {
      position: absolute;
      bottom: -15px !important;
      left: 50px;
      width: 120px;
      height: 36px;
    }
    .phy {
      width: 60%;
      height: 267px;
      padding: 20px;
      background: #37373b;
      border: 1px solid #46464a;
      border-radius: 5px;
      font-size: 14px;
      position: relative;
      button {
        width: 140px;
        @media screen and (max-width: 1700px) {
          width: 100px;
        }
      }

      .btn {
        display: flex;
        flex-wrap: wrap;
      }
      .title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 16px;
      }
      .line {
        width: 100%;
        height: 1px;
        background: #46464a;
        margin: 20px auto 10px;
      }
      .pos {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        flex-wrap: wrap;
        > div {
          margin-top: 10px;
        }
      }
      .line1 {
        margin: 20px auto 10px;
      }
      .resolution {
        display: flex;
        justify-content: space-between;
        align-items: center;
        > div {
          max-width: 90px;
          max-height: 36px;
        }
      }
      .save1 {
        left: 160px;
      }
    }
  }
}
</style>
