<template>
  <div class="gcodeManner">
    <div class="container-main">
      <!-- 头部banner -->
      <div class="head-show">
        <div class="left-feature">
          <i class="iconfont spe-icon">&#xe656;</i>
          <span class="feature-p"> Gcode按层预览</span>
        </div>
      </div>
      <!-- 下方左右分层: 左文右武 -->
      <div class="main-container">
        <!-- 右侧Gcode预览容器 -->
        <div class="left-container">
          <el-slider v-model="state.scaleVal" vertical height="515px" />
        </div>
        <!-- Gcode预览容器 -->
        <div
          id="middle-container"
          v-loading="state.loading"
          element-loading-text="gcode文件加载中..."
          :element-loading-spinner="svg"
          element-loading-svg-view-box="-10, -10, 50, 50"
          element-loading-background="rgba(255, 255, 255, 0.8)"
        ></div>
        <div class="right-container">
          <p class="gcode-name">{{ state.gcodeName }}</p>
          <!-- 底部图解 -->
          <div class="bottom-box">
            <p>
              总层高: <span>{{ state.formatSlice.length }}</span>
            </p>
            <p>
              当前层高: <span>{{ state.layer.layerCount }}</span>
            </p>
          </div>
          <!-- 上传按钮 -->
          <div class="bottom-btn">
            <div class="btn">
              <el-upload
                :show-file-list="false"
                class="upload-demo"
                :limit="1"
                :action="state.fileUrl"
                :before-upload="handleUpload"
                :on-success="uploadSuccess"
                :on-change="handleChange"
              >
                <el-button size="small"
                  ><i class="iconfont" style="font-size: 12px">&#xe643;</i
                  >上传文件</el-button
                >
              </el-upload>
            </div>
          </div>
          <!-- 底部按钮 -->
          <div class="bottom-btn">
            <el-button>清除文件</el-button>
          </div>
        </div>
      </div>
      <div class="bottom-container">
        <div class="left-control">
          <span class="spe-span">层数: </span>
          <!-- 层的旋钮 -->
          <div class="slide-con">
            <el-slider
              v-model="state.layer.layerCount"
              :min="state.layer.min"
              :max="state.layerCountMax"
              :step="state.layer.step"
            />
          </div>
          <div class="brInfo">{{ state.layer.layerCount }}</div>
        </div>
        <div class="left-control">
          <span class="spe-span">步数: </span>
          <!-- 步的旋钮 -->
          <div class="slide-con">
            <el-slider
              v-model="state.step.stepCount"
              :min="state.step.min"
              :max="state.stepCountMax"
              :step="state.step.step"
            />
          </div>
          <div class="brInfo">{{ state.step.stepCount }}</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script setup>
import {
  getCurrentInstance,
  onMounted,
  onUnmounted,
  reactive,
  watch,
} from "vue";
import GcodeLoader from "@/utils/gcode/gcodeScene";
import { ElMessage } from "element-plus";
const { proxy } = getCurrentInstance();
const svg = `
        <path class="path" d="
          M 30 15
          L 28 17
          M 25.61 25.61
          A 15 15, 0, 0, 1, 15 30
          A 15 15, 0, 1, 1, 27.99 7.5
          L 15 15
        " style="stroke-width: 4px; fill: rgba(0, 0, 0, 0)"/>
      `;
let state = reactive({
  // 是否执行了一次(潜行二次将被禁止)
  runFlag: false,
  isGcode: true,
  loading: false,
  layer: {
    layerCount: 1,
    min: 0,
    max: 100,
    step: 1,
  },
  fileUrl: "http://127.0.0.1:80",
  gcodeUrl: "",
  step: {
    stepCount: 0,
    min: 0,
    max: 100,
    step: 1,
  },
  scaleVal: 80,
  gcodeName: "",
  gcodeLoader: null,
  // Gcode数组
  formatSlice: [],
  // 总层数
  layerCountMax: 0,
  // 总步数
  stepCountMax: 0,
});
// 侦听器: 滑动组件(层、步)变化时,更新gcode视图。
watch(
  () => state.layer.layerCount,
  (val) => {
    // 仅当 层数数据变化时 触发
    // 超过原有层数,直接退出监听
    if (val > proxy.layerCountMax) {
      return;
    }
    if (!proxy.gcodeLoader) {
      return;
    }
    console.log("层数数据变化", state.formatSlice.length);
    // 此层的步数区间
    const layerInnerStepStart = state.formatSlice[val - 1];
    const layerInnerStepEnd = state.formatSlice[val];
    proxy.layerInnerStepStart = layerInnerStepStart;
    proxy.layerInnerStepEnd = layerInnerStepEnd;
    // 此层的步数差 + 步数初始挂载总步数
    state.stepCountMax = state.step.stepCount =
      layerInnerStepEnd - layerInnerStepStart;
    proxy.gcodeLoader.changeUniformsStatus(
      layerInnerStepStart,
      layerInnerStepEnd
    );
  }
);
watch(
  // 步数变化
  () => state.step.stepCount,
  (val1) => {
    console.log("步数数据变化", val1);
    proxy.gcodeLoader.changeUniformsStatus(
      proxy.layerInnerStepStart,
      proxy.layerInnerStepStart + val1
    );
  }
);
const handleChange = (file) => {
  console.log("什么文件", file.name);
  state.gcodeName = file.name;
  // 初次执行文件,需预载,则执流程,否,不与二次资源消耗
  if (!state.runFlag && state.isGcode) {
    state.loading = true;
    console.log("执行几次", state.gcodeUrl, state.runFlag);
    state.gcodeUrl = URL.createObjectURL(file.raw);
    proxy.gcodeLoader.renderFromUrl(state.gcodeUrl);
    state.runFlag = true;
  }
};
const handleUpload = (file) => {
  const ext = file.name.substring(file.name.lastIndexOf(".") + 1);
  state.isGcode = true;
  if (ext.toLowerCase() !== "gcode") {
    ElMessage.warning("请上传gcode文件!");
    state.isGcode = false;
    return;
  }
};
const uploadSuccess = () => {
  ElMessage.success("导入成功");
};
// 页面关闭之前处理逻辑
onUnmounted(() => {
  proxy.gcodeLoader.cleanAll();
  proxy.gcodeLoader = null;
});
onMounted(() => {
  console.log("全局axios绑定", proxy.$axios);
  state.socket = proxy.$webSocket;
  state.messageCenter = proxy.$messageCenter;
  // 根据文件地址渲染模型
  proxy.gcodeLoader = new GcodeLoader({
    elId: "middle-container",
  });
  // 监听加载Gcode的数据返回,用作动画处理相关
  proxy.gcodeLoader.on("formatSlice", (model) => {
    // gcode核心数据
    state.formatSlice = model;
    // 总层数
    state.layerCountMax = model.length - 1;
    // 总步数
    state.stepCountMax = state.formatSlice[state.layerCountMax];
    console.log("总层数", state.layerCountMax);
    console.log("总步数", state.stepCountMax);
    // 拿到一轮得数据之后,关闭loading,重新开启文件上传校验Flag
    state.loading = false;
    setTimeout(() => {
      state.runFlag = false;
    }, 3000);
  });
  // 监听3D类初始化完毕
  proxy.gcodeLoader.on("init", function () {
    console.log("worker启动啦");
    // this.renderFromUrl(
    //   "https://file-cdn-dev.creality.com/gcodePreview/fangKu.gcode"
    // );
  });
});
</script>
<style lang="scss" scoped>
.gcodeManner {
  padding-top: 70px;
  .container-main {
    position: relative;
    min-width: 1200px;
    width: 97.8%;
    height: 840px;
    margin: auto;
    @include theme_layout();
    .head-show {
      width: 100%;
      height: 41px;
      display: flex;
      justify-content: space-between;
      background: #46464a;
      border-bottom: 1px solid #46464a;
      border-radius: 5px;
      .left-feature {
        padding: 12px 15px;
        font-size: 16px;
        font-weight: normal;
        color: #ffffff;
        span {
          padding-left: 5px;
        }
        .spe-icon {
          color: #dbdbe0;
        }
      }
    }
    .main-container {
      // width: 1200px;
      display: flex;
      justify-content: flex-start;
      .left-container {
        width: 40px;
        height: 515px;
        display: flex;
        align-items: center;
        padding: 60px 0px 0px 20px;
        :deep().el-slider {
          margin-top: 0;
          .el-slider__runway {
            background: #4f4f54;
            width: 10px;
            .el-slider__bar {
              background: #00a0e9;
              width: 10px;
            }
            .el-slider__button-wrapper {
              bottom: 78%;
              left: -8px;
              width: 20px;
              height: 20px;
              background: #ffffff;
              border: 3px solid #37373b;
              border-radius: 50%;
              .el-slider__button {
                background: none;
              }
            }
          }
        }
      }
      #middle-container {
        width: 52%;
        height: 605px;
        background: #37373b;
        border: 1px solid #4f4f54;
        border-radius: 5px;
        margin-top: 20px;
      }
      .right-container {
        width: 300px;
        height: 175px;
        .gcode-name {
          text-align: left;
          padding: 21px 0px 0px 20px;
        }
        .bottom-box {
          width: 300px;
          height: 100px;
          background: #37373b;
          border: 1px solid #4f4f54;
          border-radius: 5px;
          text-align: left;
          margin: 9px 0px 20px 20px;
          p {
            margin-top: 20px;
            padding-left: 20px;
          }
        }
        .bottom-btn {
          .el-button {
            width: 300px;
            height: 36px;
            border-radius: 5px;
            margin-left: 20px;
            margin-bottom: 20px;
          }
        }
      }
    }
    .bottom-container {
      width: 608px;
      margin: 20px 0px 0px 60px;
      .left-control {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .spe-span {
          font-size: 14px;
          @include theme_font();
        }
        .slide-con {
          :deep().el-slider {
            width: 557px;
            .el-slider__runway {
              background: #4f4f54;
              .el-slider__bar {
                background: #00a0e9;
              }
            }
          }
        }
        .brInfo {
          position: absolute;
          left: 36.6%;
          font-size: 16px;
          @include theme_font();
        }
      }
    }
  }
}
</style>
