<template>
  <div class="main_container">
    <div class="Limit_switch">
      <HeaderBar :title="'限位/位置/开关'" :icon="'&#xe61e;'" />
      <!-- 断料检测开关 -->
      <div class="func-switch">
        <div class="container_1">
          <span>断料检测开关</span>
          <el-switch
            v-model="state.switchOn"
            size="large"
            @change="materialChange"
          />
        </div>
        <div class="container_1">
          <span>断电续打开关</span>
          <el-switch
            v-model="state.powerLoss"
            size="large"
            @change="powerChange"
          />
        </div>
      </div>
      <div class="line"></div>
      <!-- 限位开关监测 -->
      <div class="check_onbtn">
        <div class="test">
          <span>限位开关检测</span>
          <el-button><i class="iconfont">&#xe63c;</i></el-button>
        </div>
        <div class="xyz">
          <div v-for="item in state.func" :key="item.label">
            <span>{{ item.label }}</span>
            <el-button>{{ item.btn }}</el-button>
          </div>
        </div>
      </div>
    </div>
    <!-- 谐振传感器状态 -->
    <ResonantSensor></ResonantSensor>
  </div>
</template>

<script setup>
import HeaderBar from "@/components/HeaderBar";
// 谐振传感器状态
import ResonantSensor from "./ResonantSensor.vue";
import { onMounted, getCurrentInstance, reactive, watch } from "vue";
import { usePrepareStore } from "@/stores";
const store = usePrepareStore();

const state = reactive({
  switchOn: !!store.materialDetect,
  powerLoss: false,
  socket: null,
  func: [
    {
      label: "X",
      btn: "OPEN",
    },
    {
      label: "Y",
      btn: "OPEN",
    },
    {
      label: "Z",
      btn: "TRIGGERED",
    },
  ],
});
watch(store.fan, (val) => (state.switchOn = !!val));
watch(store.powerLossSw, (val) => (state.powerLoss = !!val));

const materialChange = (val) => {
  state.socket.sendMsg({
    method: "set",
    params: {
      materialDetect: +val,
    },
  });
};

const powerChange = (val) => {
  state.socket.sendMsg({
    method: "set",
    params: {
      powerLossSw: +val,
    },
  });
};

onMounted(() => {
  const { proxy } = getCurrentInstance();
  state.socket = proxy.$webSocket;
});
</script>
<style lang="scss" scoped>
.main_container {
  width: 37.8%;
  .Limit_switch {
    width: 100%;
    height: 328px;
    @include theme_layout();
    .func-switch {
      display: flex;
    }
    .container_1 {
      width: 238px;
      height: 35px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding-left: 21px;
      padding: 32px 0px 0px 21px;
      span {
        display: inline-block;
        font-size: 14px;
        font-weight: bold;
        color: #ffffff;
      }
      .el-switch {
        margin-left: 10px;
      }
    }
    .line {
      width: 685px;
      height: 1px;
      background: #46464a;
      margin: 20px auto 20px;
    }
    .check_onbtn {
      display: flex;
      font-size: 14px;
      margin-left: 20px;
      display: flex;
      flex-wrap: wrap;
      .test {
        margin-top: 10px;
        span {
          margin-right: 5px;
        }
        button {
          width: 30px;
          height: 30px;
          border-radius: 50%;
          margin-right: 39px;
          .iconfont {
            font-size: 12px;
          }
        }
      }
      .xyz {
        display: flex;
        margin-top: 10px;

        > div {
          margin-right: 30px;
        }
        span {
          margin-right: 5px;
        }
      }
    }
  }
}
</style>
