var Module = {};
var weblib;
var layout;
var platformSize = {
  width: 300,
  height: 300,
};
// cxbin格式的work事件监听
var CMD_RESULT = {
  TRIMESH_ARRAY_CMD: "getTriMeshFromArray",
  TRIMESH_GEOBUFFER_CMD: "getGeometryBuffer",
  TRIMESH_GEO_CMD: "getGeometryFromTriMesh",
  TRIMESH_DESTORY_CMD: "destoryTriMesh",
  TRIMESH_ARRAY_RESULT: "getTriMeshFromArrayResult",
  TRIMESH_GEOBUFFER_RESULT: "getGeometryResult",
  TRTRIMESH_GEO_RESULT: "geometryFromTriMeshResult",
  // webGcode预览
  GCODE_RESULT: "gcodeParseResult",
  GCODE_HEAD_RESULT: "gcodeHeadResult",
};
function Encodeuint8arr(myString) {
  return new TextEncoder("utf-8").encode(myString);
}
// // 线上cdn .wasm 文件
// const fileCnd =
//   process.env.NODE_ENV === "international"
//     ? "https://file2-cdn.creality.com/crealityCloud/"
//     : "https://file-cdn.creality.com/crealityCloud/";

// // 初始定义module容器: 线上cdn版本迭代,module.js里面改 wasmBinaryFile="WeblibModule.wasm" 的值尾缀加上版本号, 同时要与给cdn同事的wasm文件名保持一致。
// Module["locateFile"] = function (wasmName) {
//   console.log(`${fileCnd}${wasmName}`, "`${fileCnd}${wasmName}`");
//   return `${fileCnd}${wasmName}`;
// };

const WeblibModule = require("../wasm/WeblibModule.js");
// const WeblibModule = {};

function Matrix4ToFxform(matrix) {
  var b = matrix;
  return new Module.fxform(
    b[0],
    b[1],
    b[2],
    b[3],
    b[4],
    b[5],
    b[6],
    b[7],
    b[8],
    b[9],
    b[10],
    b[11],
    b[12],
    b[13],
    b[14],
    b[15]
  );
}
function copy(src, offset, length) {
  var dst = new ArrayBuffer(length);
  new Uint8Array(dst).set(new Uint8Array(src, offset, length));
  return dst;
}
onmessage = function (ev) {
  // 也可以是self.onmessage
  // 工作线程收到主线程的ev.data
  if (ev.data.cmd === "load") {
    WeblibModule(Module).then(function (instance) {
      Module = instance;
      var msg = {
        cmd: "loaded",
      };
      // module正式开始工作了
      postMessage(msg);
      // 主接口调用assembly类库
      weblib = new Module.Weblib();
      layout = new Module.AutoLayout();
      var c = new Module.JSLayoutResult();
      c.AutoLayoutResult = function (x, y, z, index) {
        postMessage({
          cmd: "autoLayoutResult",
          data: {
            location: {
              x: x,
              y: y,
              z: z,
              index: index,
            },
          },
        });
      };
      c.LayoutOneResult = function (x, y, z, uuid) {
        postMessage({
          cmd: "onLayoutOneResult",
          data: {
            location: {
              x: x,
              y: y,
              z: z,
              uuid: Module.UTF8ToString(uuid),
            },
          },
        });
      };
      layout.setResultCallback(c);
    });
  } else if (ev.data.cmd === "convex2DPolygon") {
    var result = new Module.ConvexHullResult();
    try {
      weblib.convex2DPolygon(
        ev.data.trimesh,
        Matrix4ToFxform(ev.data.matrix),
        result
      );
      let convexPoints = new Float32Array(
        Module.HEAPF32.buffer,
        result.hullPointsArray.ptr,
        result.size * 3
      ).slice(0);
      postMessage({
        cmd: "convex2DPolygonResult",
        data: {
          convexPoints: convexPoints,
          uuid: ev.data.uuid,
        },
      });
      Module.destroy(result);
    } catch (e) {
      console.log(e);
    }
  } else if (ev.data.cmd === "getTriMeshFromArray") {
    var trimesh = weblib.fromArray(ev.data.posArray, ev.data.posArray.length);
    postMessage({
      cmd: "getTriMeshFromArrayResult",
      data: {
        trimesh: trimesh,
        uuid: ev.data.uuid,
      },
    });
  } else if (ev.data.cmd === "autoLayout") {
    layout.clear();
    ev.data.hullArray.forEach(function (hull) {
      var tri = layout.fromArray(hull, hull.length);
      layout.appendModel(tri, 0, 0);
    });
    layout.layout(platformSize.width, platformSize.height, 2);
    postMessage({
      cmd: "autoLayoutFinish",
    });
  } else if (ev.data.cmd === "autoLayoutOne") {
    layout.clear();
    ev.data.hullArray.forEach(function (hullObject) {
      var tri = layout.fromArray(
        hullObject.hulldata,
        hullObject.hulldata.length
      );
      layout.appendModel(tri, hullObject.x, hullObject.y);
    });
    var hull = layout.fromArray(
      ev.data.convexPoints,
      ev.data.convexPoints.length
    );

    layout.layoutOne(
      ev.data.uuid,
      hull,
      platformSize.width,
      platformSize.height,
      2
    );
  } else if (ev.data.cmd === "setSize") {
    platformSize = ev.data.size;
  } else if (ev.data.cmd === "modelShell") {
    const { hollow, triMesh, uuid, matrix } = ev.data;
    let resultTrimesh, Shollow, JSTracer;

    Shollow = new Module.HollowingParameter();
    Shollow.min_thickness = hollow.fOffsetThr; //hollow.fOffsetThr
    // Shollow = new Module.SHollowAttrit()1
    // Shollow.fVoxelSize = hollow.fVoxelSize
    // Shollow.fOffsetThr = hollow.fOffsetThr
    // Shollow.bDiscretizeFlag = hollow.bDiscretizeFlag
    // Shollow.bMultiSampleFlag = hollow.bMultiSampleFlag
    // Shollow.bAbsDistFlag = hollow.bAbsDistFlag
    JSTracer = new Module.JSTracer();

    JSTracer.progress = function (r) {
      postMessage({
        cmd: "modelShellProgress",
        progress: parseInt(+r * 100),
      });
      console.log(r, "progress");
    };
    JSTracer.interrupt = function () {};
    JSTracer.message = function (msg) {
      console.log(Module.UTF8ToString(msg), "msg");
    };
    JSTracer.failed = function (msg) {
      console.log(Module.UTF8ToString(msg), "failedmsg");
    };
    JSTracer.success = function (msg) {
      console.log(Module.UTF8ToString(msg), "successmsg");
    };
    //analyze = new Module.CHollowAnalyze();
    var triMeshObj = Module.castObject(triMesh, Module.TriMesh);
    try {
      resultTrimesh = weblib.HollowModel2(
        triMeshObj,
        Matrix4ToFxform(matrix.elements),
        Shollow,
        JSTracer
      );
    } catch (error) {
      console.log(error, resultTrimesh, "resultTrimesh");
      postMessage({
        cmd: "modelShellFailed",
      });
      return;
    }
    if (resultTrimesh.ptr == 0) {
      return;
    }

    // Module.destroy(triMeshObj);
    var triMeshData = new Module.TriMeshData();
    weblib.getTriMeshData(resultTrimesh, triMeshData);
    var geoPoints = new Float32Array(
      Module.HEAPF32.buffer,
      triMeshData.verticesArray.ptr,
      triMeshData.verticeSize * 3
    );
    var geoFaces = new Int32Array(
      Module.HEAPF32.buffer,
      triMeshData.facesArray.ptr,
      triMeshData.facesSize * 3
    );
    Module.destroy(triMeshData);
    postMessage({
      cmd: "modelShellResult",
      data: {
        indices: Array.from(geoFaces),
        geoPoints: Array.from(geoPoints),
        originTriMesh: triMesh,
        triMesh: resultTrimesh,
        uuid: uuid,
        matrix,
      },
    });
  } else if (ev.data.cmd === "modelDrill") {
    let { matrix, param, triMesh, uuid, matrixWorld, originTriMesh } = ev.data;
    let JSTracer, resultTrimesh, DrillParam;
    JSTracer = new Module.JSTracer();
    DrillParam = new Module.DrillParam();
    DrillParam.cylinder_resolution = param.cylinder_resolution;
    DrillParam.cylinder_radius = param.cylinder_radius;
    DrillParam.cylinder_depth = param.cylinder_depth;
    DrillParam.cylinder_startPos.x = param.cylinder_startPos.x;
    DrillParam.cylinder_startPos.y = param.cylinder_startPos.y;
    DrillParam.cylinder_startPos.z = param.cylinder_startPos.z;
    DrillParam.cylinder_Dir.x = param.cylinder_Dir.x;
    DrillParam.cylinder_Dir.y = param.cylinder_Dir.y;
    DrillParam.cylinder_Dir.z = param.cylinder_Dir.z;

    JSTracer.progress = function (r) {
      console.log(r, "rrr");
    };
    JSTracer.interrupt = function () {};
    JSTracer.message = function (msg) {
      console.log(Module.UTF8ToString(msg), "msg");
    };
    JSTracer.failed = function (msg) {
      console.log(Module.UTF8ToString(msg), "failedmsg");
    };
    JSTracer.success = function () {
      console.log("drill success");
    };
    weblib.appendDrillParam(DrillParam);
    resultTrimesh = weblib.drillMultCylinder(
      triMesh,
      Matrix4ToFxform(matrix),
      JSTracer
    );
    if (resultTrimesh.ptr == 0) {
      console.log("drill failed");
      postMessage({
        cmd: "modelDrillResult",
        data: {
          result: 0,
        },
      });
      return;
    }
    triMeshData = new Module.TriMeshData();
    weblib.getTriMeshData(resultTrimesh, triMeshData);
    let geoPoints = new Float32Array(
      Module.HEAPF32.buffer,
      triMeshData.verticesArray.ptr,
      triMeshData.verticeSize * 3
    );
    let geoFaces = new Int32Array(
      Module.HEAPF32.buffer,
      triMeshData.facesArray.ptr,
      triMeshData.facesSize * 3
    );
    Module.destroy(triMeshData);
    postMessage({
      cmd: "modelDrillResult",
      data: {
        result: 1,
        indices: Array.from(geoFaces),
        geoPoints: Array.from(geoPoints),
        originTriMesh: originTriMesh,
        triMesh: resultTrimesh,
        lastTrimesh: triMesh,
        uuid: uuid,
        matrix: matrixWorld,
      },
    });
  } else if (ev.data.cmd === "getGeometryFromTriMesh") {
    const { triMesh, uuid } = ev.data;
    triMeshData = new Module.TriMeshData();
    weblib.getTriMeshData(triMesh, triMeshData);
    let geoPoints = new Float32Array(
      Module.HEAPF32.buffer,
      triMeshData.verticesArray.ptr,
      triMeshData.verticeSize * 3
    );
    let geoFaces = new Int32Array(
      Module.HEAPF32.buffer,
      triMeshData.facesArray.ptr,
      triMeshData.facesSize * 3
    );
    Module.destroy(triMeshData);
    postMessage({
      cmd: "geometryFromTriMeshResult",
      data: {
        indices: Array.from(geoFaces),
        geoPoints: Array.from(geoPoints),
        triMesh: triMesh,
        originTriMesh: triMesh,
        uuid: uuid,
      },
    });
    // 老版本对应新版本代码模式,work通信
  } else if (ev.data.cmd === CMD_RESULT.TRIMESH_GEOBUFFER_CMD) {
    let bufferName = "geoBuffer" + Math.random() + ev.data.ext;
    let triMesh, triMeshData, geoPoints, geoFaces;
    triMeshData = new Module.TriMeshData();
    Module.FS.writeFile(bufferName, new Uint8Array(ev.data.buffer));
    triMesh = weblib.getTriMeshFromFilename(bufferName);
    Module.FS.unlink(bufferName);
    weblib.getTriMeshData(triMesh, triMeshData);
    geoPoints = new Float32Array(
      Module.HEAPF32.buffer,
      triMeshData.verticesArray.ptr,
      triMeshData.verticeSize * 3
    );
    geoFaces = new Int32Array(
      Module.HEAPF32.buffer,
      triMeshData.facesArray.ptr,
      triMeshData.facesSize * 3
    );
    var uvs = new Float32Array(
      Module.HEAPF32.buffer,
      triMeshData.uvsArray.ptr,
      triMeshData.uvsSize * 2
    );
    var textureWidth = 0;
    var textureHeight = 0;
    var textureBuffer = null;
    if (triMeshData.imagedata.ptr) {
      textureWidth = triMeshData.imagedata.width;
      textureHeight = triMeshData.imagedata.height;
      textureBuffer = new Uint8Array(
        Module.HEAPF32.buffer,
        triMeshData.textureBuffer,
        textureWidth * textureHeight
      );
    }
    const indices = copy(
      geoFaces.buffer,
      geoFaces.byteOffset,
      geoFaces.length * 4
    );
    const points = copy(
      geoPoints.buffer,
      geoPoints.byteOffset,
      geoPoints.length * 4
    );
    const uvsdata = copy(uvs.buffer, uvs.byteOffset, uvs.length * 4);
    const texture =
      textureBuffer == null
        ? new ArrayBuffer(0)
        : copy(
            textureBuffer.buffer,
            textureBuffer.byteOffset,
            textureBuffer.length
          );
    Module.destroy(triMeshData.mesh);
    Module.destroy(triMeshData.imagedata);
    Module.destroy(triMeshData);
    postMessage(
      {
        cmd: CMD_RESULT.TRIMESH_GEOBUFFER_RESULT,
        indices: indices,
        geoPoints: points,
        uvs: uvsdata,
        textureWidth: textureWidth,
        textureHeight: textureHeight,
        textureBuffer: texture,
        triMesh,
        model: ev.data.model,
      },
      [indices, points, uvsdata, texture]
    );
  } else if (ev.data.cmd === CMD_RESULT.TRIMESH_GEO_CMD) {
    const { triMesh, uuid } = ev.data;
    triMeshData = new Module.TriMeshData();
    weblib.getTriMeshData(triMesh, triMeshData);
    const geoPoints = new Float32Array(
      Module.HEAPF32.buffer,
      triMeshData.verticesArray.ptr,
      triMeshData.verticeSize * 3
    );
    const geoFaces = new Int32Array(
      Module.HEAPF32.buffer,
      triMeshData.facesArray.ptr,
      triMeshData.facesSize * 3
    );
    const uvs = new Float32Array(
      Module.HEAPF32.buffer,
      triMeshData.uvsArray.ptr,
      triMeshData.uvsSize * 2
    );
    if (triMeshData.imagedata) {
      let textureWidth = triMeshData.imagedata.width;
      let textureHeight = triMeshData.imagedata.height;
      if (triMeshData.textureBuffer.ptr > 0) {
        textureBuffer = new Uint8Array(
          Module.HEAPF32.buffer,
          triMeshData.imagedata.data,
          textureWidth * textureHeight
        );
      }
      Module.destroy(triMeshData.imagedata);
    }
    Module.destroy(triMeshData.mesh);
    Module.destroy(triMeshData);
    postMessage({
      cmd: CMD_RESULT.TRTRIMESH_GEO_RESULT,
      data: {
        indices: Array.from(geoFaces),
        geoPoints: Array.from(geoPoints),
        uvs: Array.from(uvs),
        triMesh: triMesh,
        textureBuffer: Array.from(textureBuffer),
        originTriMesh: triMesh,
        uuid: uuid,
      },
    });
  } else if (ev.data.cmd === CMD_RESULT.TRIMESH_DESTORY_CMD) {
    var object = Module.castObject(ev.data.trimesh, Module.TriMesh);
    Module.destroy(object);
    // 算法解析 gcode
  } else if (ev.data.cmd === "gcodeParse") {
    let bufferName = "1.gcode";
    let JSTracer = new Module.JSTracer();
    JSTracer.progress = function (r) {
      console.log(r, "rrr");
    };
    JSTracer.interrupt = function () {};
    JSTracer.message = function (msg) {
      console.log(Module.UTF8ToString(msg), "msg");
    };
    JSTracer.failed = function (msg) {
      console.log(Module.UTF8ToString(msg), "failedmsg");
    };
    JSTracer.success = function () {
      console.log("drill success");
    };
    let JSGCodeParseCallBack = new Module.JSGCodeParseCallBack();
    JSGCodeParseCallBack.GCodeParseResult = function (data) {
      let gcodeobj = {};
      gcodeobj.ptr = data;
      gcodeobj = Module.castObject(gcodeobj, Module.GCodeData);
      var vertexNum = gcodeobj.vertexNum;
      var positionEndArray = new Float32Array(
        Module.HEAPF32.buffer,
        gcodeobj.positionEndArray.ptr,
        vertexNum * 4
      );
      var positionBeginArray = new Float32Array(
        Module.HEAPF32.buffer,
        gcodeobj.positionBeginArray.ptr,
        vertexNum * 2
      );
      var stepArray = new Float32Array(
        Module.HEAPF32.buffer,
        gcodeobj.stepArray.ptr,
        vertexNum
      );
      var flagArray = new Float32Array(
        Module.HEAPF32.buffer,
        gcodeobj.flagArray.ptr,
        vertexNum
      );
      const positionEnd = copy(
        positionEndArray.buffer,
        positionEndArray.byteOffset,
        positionEndArray.length * 4
      );
      const positionBegin = copy(
        positionBeginArray.buffer,
        positionBeginArray.byteOffset,
        positionBeginArray.length * 4
      );
      const step = copy(
        stepArray.buffer,
        stepArray.byteOffset,
        stepArray.length * 4
      );
      const flag = copy(
        flagArray.buffer,
        flagArray.byteOffset,
        flagArray.length * 4
      );

      postMessage(
        {
          cmd: CMD_RESULT.GCODE_RESULT,
          data: {
            positionEndArray: positionEnd,
            positionBeginArray: positionBegin,
            stepArray: step,
            flagArray: flag,
          },
        },
        [positionEnd, positionBegin, step, flag]
      );
    };
    JSGCodeParseCallBack.HeaderResult = function (data) {
      let gcodeMeshData = {};
      gcodeMeshData.ptr = data;
      gcodeMeshData = Module.castObject(gcodeMeshData, Module.GCodeMeshData);
      var totallayer = gcodeMeshData.layer;
      var layerArray = new Int32Array(
        Module.HEAPF32.buffer,
        gcodeMeshData.layerIndex.ptr,
        totallayer
      );
      const layers = copy(
        layerArray.buffer,
        layerArray.byteOffset,
        layerArray.length * 4
      );
      postMessage(
        {
          cmd: CMD_RESULT.GCODE_HEAD_RESULT,
          data: {
            totallayer: totallayer,
            layers: layers,
          },
        },
        [layers]
      );
    };
    // 新老版本: data: ev.data
    Module.FS.writeFile(bufferName, Encodeuint8arr(ev.data.buffer));
    // var gcodeMeshData = new Module.GCodeMeshData();
    // 新老版本: Module.weblibInst : weblib
    weblib.getGCodeDataFromFilename(bufferName, JSGCodeParseCallBack, JSTracer);
    Module.FS.unlink(bufferName);
    // var gcodeArray = gcodeMeshData.gcodeArray;
  }
};
