<template>
  <KeepAlive>
    <div class="container comp-printHistory" @click.left="menuShow = false">
      <!-- 左边容器 -->
      <div class="container-left">
        <HeaderBar :title="'打印统计'" :icon="'&#xe653;'" />
        <!-- 数据汇总展示区域 -->
        <div class="table-warp">
          <div
            class="table-list"
            v-for="(item, index) in state.tableList"
            :key="index"
          >
            <div class="table-info">
              <div class="inner-one">
                <p class="left-p">{{ item.showInfo1 }}</p>
                <p class="right-p">{{ item.data1 }}</p>
              </div>
              <div class="inner-one">
                <p class="left-p">{{ item.showInfo2 }}</p>
                <p class="right-p">{{ item.data2 }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 右边容器 -->
      <div class="container-right">
        <div class="inner-con">
          <div
            v-for="(item, index) in state.moduleList"
            :class="['inner-top', { 'inner-bg': tabClick == item.idx }]"
            :key="index"
            @click="switchModule(item)"
          >
            <i :class="['iconfont', item.iconInfo]"></i>
            <span>{{ item.moduleName }}</span>
          </div>
          <!-- 搜索模块 -->
          <div class="inner-search">
            <el-input
              v-model="state.searchValue"
              size="large"
              placeholder="请输入名称"
              @change="innerSearch"
            />
            <div class="myself-icon">
              <i class="iconfont spe-icon">&#xe64f;</i>
            </div>
          </div>
          <div class="inner-btn">
            <el-button @click="innerSearch">搜索</el-button>
          </div>
        </div>
        <!-- 历史记录表格数据 -->
        <div class="tableList" v-show="tabClick === 0">
          <el-table
            :data="state.hisTableData"
            style="width: 100%"
            @row-contextmenu="rightClick"
          >
            <!-- :header-cell-style="{ textAlign: 'center' }" -->
            <el-table-column type="selection" width="55" />
            <el-table-column
              prop="filename"
              label="名称"
              width="300"
              align="left"
            />
            <el-table-column label="状态" sortable align="center">
              <template #default="scope">{{
                scope.row.printfinish == 0 ? "未完成" : "完成"
              }}</template>
            </el-table-column>
            <el-table-column
              prop="starttime"
              label="开始时间"
              sortable
              align="center"
            >
              <template #default="scope">{{
                timestampToTime(scope.row.starttime)
              }}</template>
            </el-table-column>
            <el-table-column
              prop="ctime"
              label="结束时间"
              sortable
              align="center"
            >
              <template #default="scope">{{
                timestampToTime(scope.row.starttime + scope.row.usagetime)
              }}</template>
            </el-table-column>
            <el-table-column
              prop="usagetime"
              label="总时长"
              sortable
              align="center"
            >
              <template #default="scope">{{
                formatSeconds(scope.row.usagetime)
              }}</template>
            </el-table-column>
            <el-table-column prop="size" label="大小" sortable align="center">
              <template #default="scope">{{
                (scope.row.size / 1048576).toFixed(2) + "M"
              }}</template>
            </el-table-column>
          </el-table>
        </div>
        <!-- 延时摄影表格数据 -->
        <div class="tableList" v-show="tabClick === 1">
          <el-table
            :data="state.timeTableData"
            style="width: 100%"
            @row-contextmenu="rightClick"
          >
            <!-- :header-cell-style="{ textAlign: 'center' }" -->
            <el-table-column type="selection" width="55" />
            <el-table-column prop="videoName" label="视频名称" align="left" />
            <el-table-column
              prop="recordTime"
              label="录制时间"
              sortable
              align="center"
            />
            <el-table-column
              prop="videoTime"
              label="视频时长"
              sortable
              align="center"
            />
            <el-table-column
              prop="videoSize"
              label="文件大小"
              sortable
              align="center"
            />
          </el-table>
        </div>
        <div
          class="menu"
          v-show="menuShow"
          :style="{ left: menuLeft + 'px', top: menuTop + 'px' }"
        >
          <div
            v-for="(item, index) in state.menus"
            :key="index"
            @click.stop="menuClick(item)"
          >
            {{ item.name }}
          </div>
        </div>
      </div>
      <div class="dialog">
        <el-dialog v-model="state.dialogVisible" :title="state.videoTitle">
          <video :src="state.videoUrl" controls />
        </el-dialog>
      </div>
    </div>
  </KeepAlive>
</template>
<script setup>
import { getCurrentInstance, onMounted, reactive, ref, watch } from "vue";
import {
  formatSeconds,
  timestampToTime,
  formatSize,
  downFile,
} from "@/utils/public";
import HeaderBar from "@/components/HeaderBar";
import { useMainStore } from "@/stores";

const { proxy } = getCurrentInstance();
let url = `http://${proxy.$ip}:80`;

let state = reactive({
  controlFlag: true,
  standInfo: "暂停",
  startInfo: "继续",
  stopInfo: "终止",
  showTable: true,
  menus: [
    { name: "清除历史记录", operType: 1 },
    { name: "删除文件", operType: 2 },
  ],
  // 搜索的值
  searchValue: "",
  searchIcon: "icon-a-home1",
  // 左侧容器数据
  tableList: [
    {
      showInfo1: "总任务量",
      data1: 0,
      showInfo2: "最长工作时间",
      data2: 0,
    },
    {
      showInfo1: "总使用耗材长度",
      data1: 0,
      showInfo2: "平均每次使用耗材长度",
      data2: 0,
    },
    {
      showInfo1: "总时间",
      data1: "7小时24分19秒",
      showInfo2: "平均每次打印",
      data2: 0,
    },
  ],
  // 右侧tab切换
  moduleList: [
    {
      moduleName: "历史打印列表",
      idx: 0,
      moduleInfo: "history",
      iconInfo: "icon-a-xingzhuang14",
    },
    {
      moduleName: "延时摄影",
      idx: 1,
      moduleInfo: "timeLapse",
      iconInfo: "icon-video",
    },
  ],
  // 历史记录的表格数据
  hisTableData: [],
  cloneHisData: [],
  // 延时摄影的表格数据
  timeTableData: [],
  cloneTimeData: [],
  videoUrl: "",
  videoTitle: "",
  dialogVisible: false,
});

const menuShow = ref(false);
const menuLeft = ref(0);
const menuTop = ref(0);
const rowValue = ref("");
const rightClick = (row, column, event) => {
  event.preventDefault();
  const refDom = document.querySelector(".comp-printHistory");
  const { top, left } = refDom.getBoundingClientRect();
  const { clientX, clientY } = event;
  menuLeft.value = clientX - left;
  menuTop.value = clientY - top;
  menuShow.value = true;
  rowValue.value = row;
};

const menuClick = (item) => {
  menuShow.value = false;
  let s = rowValue.value.videoName;
  let videoName = s.substring(s.lastIndexOf("/") + 1);
  if (item.operType === 3) play();
  if (item.operType === 4) downFile(`${url}/downloads/${videoName}`, videoName);
  // if (item.operType === 2) deleteFile();
  // if (item.operType === 1) printFile();
};

const play = () => {
  let s = rowValue.value.videoName;
  let videoName = s.substring(s.lastIndexOf("/") + 1);
  state.videoUrl = `${url}/downloads/${videoName}`;
  state.videoTitle = s.substring(s.lastIndexOf("/") + 1);
  state.dialogVisible = true;
};

const tabClick = ref(0);
// 模块切换: 历史记录和延时摄影
function switchModule(item) {
  tabClick.value = item.idx;
  state.searchValue = "";
  const val = item.idx === 0 ? "reqHistory" : "reqElapseVideoList";
  state.socket.sendMsg({
    method: "get",
    params: { [val]: 1 },
  });
  if (item.idx === 1) {
    state.menus.push(
      ...[
        { name: "播放视频", operType: 3 },
        { name: "下载视频", operType: 4 },
      ]
    );
  } else {
    state.menus = state.menus.filter((f) => f.operType !== 3);
    state.menus = state.menus.filter((f) => f.operType !== 4);
  }
}
// 搜索按钮
function innerSearch() {
  if (tabClick.value === 0) {
    if (!state.searchValue) {
      state.hisTableData = state.cloneHisData;
    } else {
      state.hisTableData = [];
      for (const item of state.cloneHisData) {
        if (item.filename.includes(state.searchValue)) {
          state.hisTableData.push(item);
        }
      }
    }
  } else {
    if (!state.searchValue) {
      state.timeTableData = state.cloneTimeData;
    } else {
      state.timeTableData = [];
      for (const item of state.cloneTimeData) {
        if (item.videoName.includes(state.searchValue)) {
          state.timeTableData.push(item);
        }
      }
    }
  }
}

const store = useMainStore();
watch(
  () => store.elapseVideoList,
  (elapseVideoList) => {
    if (!elapseVideoList || !elapseVideoList.length) return;
    state.timeTableData = [];
    for (const item of elapseVideoList) {
      state.timeTableData.push({
        videoName: item.video,
        recordTime: timestampToTime(item.starttime),
        videoTime: formatSeconds(item.duration),
        videoSize: formatSize(item.size),
      });
    }
    state.cloneTimeData = JSON.parse(JSON.stringify(state.timeTableData));
  },
  { immediate: true }
);
watch(
  () => store.historyList,
  (historyList) => {
    if (!historyList || !historyList.length) return;
    state.timeTableData = [];
    state.cloneHisData = JSON.parse(JSON.stringify(historyList));
  },
  { immediate: true }
);

onMounted(() => {
  const { proxy } = getCurrentInstance();
  state.socket = proxy.$webSocket;
  state.messageCenter = proxy.$messageCenter;
  switchModule({ idx: 0 });
  // 监听到websocket数据
  state.messageCenter.on("message_change", (data) => {
    if (data !== "ok") {
      const responData = JSON.parse(data);
      if (responData.totalJob) {
        const histList = responData.historyList;
        const conArr = [];
        histList.forEach((element) => {
          conArr.push(element.usagetime);
        });
        const maxUseTime = Math.max.apply(null, conArr);
        // 左侧数据赋值: 总任务量
        state.tableList[0].data1 = responData.totalJob;
        // 左侧数据赋值: 最长工作时间
        state.tableList[0].data2 = formatSeconds(maxUseTime);
        // 左侧数据赋值: 总使用耗材长度
        state.tableList[1].data1 = responData.totalUsageMaterial + "米";
        // 左侧数据赋值: 平均使用耗材长度
        state.tableList[1].data2 =
          (responData.totalUsageMaterial / responData.totalJob).toFixed(2) +
          "米";
        // 左侧数据赋值: 总时间
        state.tableList[2].data1 = formatSeconds(responData.totalUsageTime);
        // 左侧数据赋值: 平均打印时间
        const leveTime = (
          responData.totalUsageTime / responData.totalJob
        ).toFixed(2);
        state.tableList[2].data2 = formatSeconds(leveTime);
        // 右侧历史数据Table
        state.hisTableData = histList;
      }
    }
  });
});
</script>
<style lang="scss" scoped>
.container {
  width: 100%;
  display: flex;
  .container-left {
    width: 22%;
    min-width: 400px;
    height: 487px;
    margin: 20px 0px 0px 20px;
    @include theme_layout();
    .table-warp {
      width: 90%;
      margin: auto;
      // margin-top: 20px;
      .table-list {
        width: 100%;
        height: 114px;
        background: #323236;
        border: 1px solid #46464a;
        border-radius: 5px;
        margin-top: 20px;
        .table-info {
          width: 338px;
          height: 100%;
          margin: auto;
          .inner-one {
            width: 100%;
            height: 55px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .left-p {
              font-size: 14px;
              font-family: Source Han Sans CN;
              font-weight: 400;
              color: #c5c5c5;
            }
            .right-p {
              font-size: 18px;
              font-family: Source Han Sans CN;
              font-weight: 500;
              color: #ffffff;
            }
          }
          .inner-one:nth-child(1) {
            border-bottom: 1px solid #46464a;
          }
        }
      }
    }
  }
  .container-right {
    width: 74.7%;
    min-width: 1100px;
    min-height: 400px;
    height: calc(100vh - 100px);
    margin: 20px;
    @include theme_layout();
    .inner-con {
      height: 40px;
      display: flex;
      align-items: center;
      // border-bottom: 1px solid #46464a;
      background: #37373b;
      // border: 1px solid #0f0f10;
      box-shadow: 0px 6px 6px 0px rgba(36, 36, 39, 0.5);
      border-radius: 5px;
      .inner-top:hover {
        background: #525258;
        .iconfont {
          color: #fff;
        }
        span {
          color: #fff;
        }
      }
      .inner-top {
        width: 144px;
        height: 36px;
        border-radius: 5px;
        line-height: 36px;
        cursor: pointer;
        .iconfont {
          color: #cbcbcc;
        }
        span {
          color: #cbcbcc;
          margin-left: 7px;
        }
      }
      .inner-bg {
        background: #1e9be2;
        span {
          color: #ffffff;
        }
      }
      .inner-search {
        position: relative;
        margin-left: 54.9%;
        .el-input {
          width: 258px;
          height: 30px;
          font-size: 12px;
          padding-left: 20px;
          background: #37373b;
          border: 1px solid #4f4f54;
          border-radius: 15px;
        }
        .myself-icon {
          position: absolute;
          position: absolute;
          top: 7px;
          left: 13px;
          color: #cbcbcc;
        }
      }
      .inner-btn {
        padding-left: 5px;
        .el-button {
          width: 66px;
          height: 30px;
          font-size: 12px;
          box-shadow: 0px 3px 3px 0px #303034;
          border-radius: 15px;
        }
      }
    }
    .tableList {
      width: 100%;
    }
  }
}
.comp-printHistory {
  position: relative;
  box-shadow: 0px 6px 6px 0px rgba(36, 36, 39, 0.5);
  padding-top: 50px;
  .menu {
    position: absolute;
    width: 160px;
    background: #ffffff;
    box-shadow: 0px 3px 3px 0px rgba(51, 51, 51, 0.5);
    border-radius: 5px;
    color: #414143;
    font-size: 12px;
    z-index: 9;
    > div {
      text-align: left;
      padding: 8px;
      cursor: pointer;
      &:first-child {
        border-radius: 5px 5px 0 0;
      }
      &:last-child {
        border-radius: 0 0 5px 5px;
      }
      &:hover {
        background: #a5dbf9;
      }
    }
  }
  :deep() .el-checkbox__input.is-disabled .el-checkbox__inner {
    background-color: transparent;
    border: none;
  }
  .dialog {
    :deep() .el-dialog {
      width: 700px;
      height: 500px;
      .el-dialog__body {
        padding: 0 20px 20px;
        height: calc(100% - 74px);
      }
      video {
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style>
